
import jsonrpcSimple, {
    comet as cometFunc,
    startSubscription as startSubscriptionFunc,
    unsubscribe as unsubscribeFunc,
} from './jsonrpc';
import {
    isDevEnv,
    isFunction,
} from './utils/functional';
import {
    getWindowName,
} from './utils/browser';
import {
    isEmptyArray,
} from './utils/array';
import {
    isObject,
    objectHasAllProps,
} from './utils/object';
import { isPositiveNumber } from './utils/number';

const INVALID_BY_MS = 10000;
const CONNECTION_LOST = 'Connection lost';
const INVALID_SESSION_ID = 'session.invalid_sessionid';

let mainCometId;
const getMainCometId = () => {
    if (!mainCometId) {
        mainCometId = getWindowName() || `main-comet-${Date.now()}`;
    }
    return mainCometId;
};

export const DEFAULT_START_WAIT_TIME = 1000;
export const SUBSCRIPTION_STATUS = {
    UNKNOWN: -1,
    STOPPED: 0,
    STARTED: 1,
    STOPPING: 2,
    STARTING: 3,
};

const hasStopped = message => (isObject(message) && message.stopped === true);

const buildParams = (args, attrs = []) => (
    attrs.reduce((params, attr) => {
        if (args[attr] !== undefined) {
            // eslint-disable-next-line no-param-reassign
            params[attr] = args[attr];
        }
        return params;
    }, {})
);

const getInvalidByTime = () => Date.now() + INVALID_BY_MS;

const getSubscriptionStatus = (sub) => {
    if (sub.started) {
        return SUBSCRIPTION_STATUS.STARTED;
    }
    if (sub.stopped) {
        return SUBSCRIPTION_STATUS.STOPPED;
    }
    return SUBSCRIPTION_STATUS.UNKNOWN;
};

const createSubscription = ({
    handle,
    path,
    status,
    tag,
}) => ({
    callbacks: [],
    handle,
    path,
    status,
    tag,
});

const getSubscriptions = (
    channel,
    {
        funcs: {
            jsonrpc = jsonrpcSimple,
        } = {},
    } = {},
) => (
    jsonrpc('get_subscriptions').then(result => (
        result.subscriptions.reduce((acc, sub) => {
            const { handle, comet_id: cometId, started } = sub;
            if (started && channel.cometId === cometId) {
                let subscription = channel.findSubscriptionByHandle(handle);
                if (!subscription) {
                    subscription = createSubscription({
                        path: (sub.params && sub.params.path) || undefined,
                        handle,
                        status: getSubscriptionStatus(sub),
                        tag: sub.tag,
                    });
                }
                if (subscription.callbacks.length === 0
                    && subscription.status === SUBSCRIPTION_STATUS.STARTED
                    && !isPositiveNumber(subscription.invalidBy)) {
                    subscription.invalidBy = getInvalidByTime();
                }
                acc.push(subscription);
            }
            return acc;
        }, [])
    ))
);

const notifyCallbacks = (message, subscription, { logger = console } = {}) => (
    subscription.callbacks.reduce((stop, cb) => {
        try {
            return cb(message, subscription) || stop;
        } catch (error) {
            if (isDevEnv()) {
                logger.log(
                    'An error occurred when notifying a callback',
                    { error, cb, subscription, message },
                );
            }
            return stop;
        }
    }, false)
);

class Comet {
    constructor(cometId) {
        this.cometId = cometId;
        this.running = false;
        this.polling = false;
        this.startPromise = undefined;
        this.subscriptions = [];
        this.errorHandlers = [];
    }

    handleError(error, subscription, { logger = console } = {}) {
        if (isEmptyArray(this.errorHandlers) && isDevEnv()) {
            logger.log(
                'Comet.handleError - no error handlers registered',
                error,
                this.cometId,
                subscription,
            );
        }
        return this.errorHandlers
            .reduce((results, handler) => {
                results.push(handler(error, this.cometId, subscription));
                return results;
            }, [])
            .some(f => f);
    }

    addErrorHandler(handler) {
        if (isFunction(handler) && !this.errorHandlers.includes(handler)) {
            this.errorHandlers.push(handler);
        }
    }

    getErrorHandlers() {
        return this.errorHandlers;
    }

    removeErrorHandler(handler) {
        if (!isFunction(handler)) {
            return;
        }
        const index = this.errorHandlers.indexOf(handler);
        if (index !== -1) {
            this.errorHandlers.splice(index, 1);
        }
    }

    isRunning() { return this.running; }

    start(wait = 0, {
        funcs: {
            getSubs = getSubscriptions,
            comet = cometFunc,
        } = {},
    } = {}) {
        if (this.startPromise) {
            return this.startPromise;
        }
        this.startPromise = new Promise((resolve) => {
            setTimeout(() => {
                const prom = getSubs(this);
                prom.then((subs) => {
                    this.subscriptions = subs;
                    this.running = true;
                    this.poll({ funcs: { comet } });
                    resolve(this.running);
                });
            }, wait);
        });
        return this.startPromise;
    }

    stop() {
        this.running = false;
        this.startPromise = undefined;
        return Promise.resolve(this.running);
    }

    findSubscription({ callback, handle, params, tag }) {
        const subscription = this.subscriptions.find((sub) => {
            if (handle !== undefined) {
                return sub.handle === handle;
            }
            if (sub.tag !== tag) {
                return false;
            }
            if (!objectHasAllProps(sub, params)) {
                return false;
            }
            if (!callback) {
                return true;
            }
            return sub.callbacks.includes(callback);
        });
        return subscription;
    }

    findSubscriptionByHandle(handle) {
        return this.findSubscription({ handle });
    }

    subscribe(
        tag,
        params,
        callback,
        { funcs: {
            jsonrpc = jsonrpcSimple,
            startSubscription = startSubscriptionFunc,
        } = {} } = {},
    ) {
        if (!this.startPromise) {
            throw new Error(`Comet channel ${this.cometId} not started!`);
        }
        return this.startPromise.then(() => {
            let subscription = this.findSubscription({ tag, params });
            if (!subscription) {
                subscription = createSubscription({
                    path: params.path,
                    callbacks: callback,
                    tag,
                });
                this.subscriptions.push(subscription);
            }

            if (!subscription.callbacks.includes(callback)) {
                subscription.callbacks.push(callback);
            }

            if (subscription.status === SUBSCRIPTION_STATUS.STARTING
                || subscription.status === SUBSCRIPTION_STATUS.STARTED
            ) {
                subscription.invalidBy = undefined;
                return Promise.resolve(subscription.handle);
            }

            subscription.status = SUBSCRIPTION_STATUS.STARTING;

            return jsonrpc(
                tag,
                { ...params, comet_id: this.cometId },
            ).then(({ handle }) => {
                const promise = params.handle === handle
                    ? Promise.resolve(handle)
                    : startSubscription(handle);
                return promise.then((subHandle) => {
                    if (!subscription.handle) {
                        subscription.handle = subHandle;
                    }
                    subscription.status = SUBSCRIPTION_STATUS.STARTED;
                    return subHandle;
                });
            });
        });
    }

    subscribeChanges(args, callback) {
        const params = buildParams(args, [
            'handle',
            'path',
            'skip_local_changes',
            'hide_changes',
            'hide_values',
        ]);
        return this.subscribe('subscribe_changes', params, callback);
    }

    subscribeCdbOper(args, callback) {
        const params = buildParams(args, ['handle', 'path']);
        return this.subscribe('subscribe_cdboper', params, callback);
    }

    subscribeUpgrade(args, callback) {
        const params = buildParams(args, ['handle']);
        return this.subscribe('subscribe_upgrade', params, callback);
    }

    subscribeMessages(args, callback) {
        const params = buildParams(args, ['handle']);
        return this.subscribe('subscribe_messages', params, callback);
    }

    unsubscribe(subscriptionInfo, {
        funcs: {
            logger = console,
            unsubscribe = unsubscribeFunc,
        } = {},
    } = {}) {
        const subscription = this.findSubscription(subscriptionInfo);
        if (!subscription) {
            if (isDevEnv()) {
                logger.log('subscription not found');
            }
            return Promise.resolve({});
        }
        const callback = subscriptionInfo.callback;
        if (!callback) {
            if (isDevEnv()) {
                logger.log('no callback to unsubscribe');
            }
            return Promise.resolve(subscription);
        }
        if (!this.startPromise) {
            return Promise.resolve(subscription);
        }
        const callbackIndex = subscription.callbacks.indexOf(callback);
        if (callbackIndex !== -1) {
            subscription.callbacks.splice(callbackIndex, 1);
        }

        if (subscription.callbacks.length > 0) {
            return Promise.resolve(subscription);
        }
        subscription.status = SUBSCRIPTION_STATUS.STOPPING;
        return unsubscribe(subscription.handle);
    }

    unsubscribeMessages(params, callback) {
        return this.unsubscribe({
            tag: 'subscribe_messages',
            params,
            callback,
        });
    }

    unsubscribeUpgrade(params, callback) {
        return this.unsubscribe({
            tag: 'subscribe_upgrade',
            params,
            callback,
        });
    }

    unsubscribeChanges(params, callback) {
        return this.unsubscribe({
            tag: 'subscribe_changes',
            params,
            callback,
        });
    }

    unsubscribeCdbOper(params, callback) {
        return this.unsubscribe({
            tag: 'subscribe_cdboper',
            params,
            callback,
        });
    }

    removeStoppedSubscriptions() {
        this.subscriptions = this.subscriptions.filter(sub => (
            sub.status !== SUBSCRIPTION_STATUS.STOPPED
        ));
    }

    handleNotifications(
        notifications,
        {
            logger = console,
            callCallbacks = notifyCallbacks,
        } = {},
    ) {
        return notifications.reduce((fatal, notification) => {
            const { handle, message } = notification;
            const subscription = this.findSubscriptionByHandle(handle);
            if (!subscription) {
                if (isDevEnv()) {
                    // eslint-disable-next-line
                    logger.log(`No subscription found for cometId "${this.cometId}": event handle ${handle}`, message);
                }
                return fatal;
            }
            if (hasStopped(message)) {
                subscription.status = SUBSCRIPTION_STATUS.STOPPED;
                return fatal;
            }
            if (subscription.callbacks.length === 0) {
                if (isDevEnv()) {
                    // eslint-disable-next-line
                    logger.log(`No callback handler for cometId "${this.cometId}": event handle ${handle}`);
                }
                return fatal;
            }
            return callCallbacks(message, subscription) || fatal;
        }, false);
    }

    handlePollResult(notifications) {
        return new Promise((resolve) => {
            const fatal = this.handleNotifications(notifications);
            this.removeStoppedSubscriptions();
            this.polling = false;
            if (this.running && !fatal) {
                this.poll();
            }
            resolve();
        });
    }

    handlePollError(error, subscription) {
        const fatal = this.handleError(error, subscription);
        if (!fatal) {
            this.poll();
        }
        return Promise.resolve();
    }

    doPoll({
        funcs: {
            comet = cometFunc,
        } = {},
    } = {}) {
        return comet(this.cometId)
            .then(result => (
                this.handlePollResult(result, { funcs: { comet } })
            ), error => (
                this.handlePollError(error, { funcs: { comet } })
            ));
    }

    unsubscribeInvalidatedSubscriptions() {
        this.subscriptions = this.subscriptions.reduce((acc, subscription) => {
            const invalidBy = subscription.invalidBy;
            if (Number.isFinite(invalidBy) && Date.now() >= invalidBy) {
                this.unsubscribe(subscription);
            } else {
                acc.push(subscription);
            }
            return acc;
        }, []);
    }

    poll() {
        if (!this.running || this.polling) {
            return Promise.resolve();
        }

        this.unsubscribeInvalidatedSubscriptions();

        this.polling = true;
        return this.doPoll();
    }
}

const channels = {};
const hasCometChannel = cometId => (
    Object.keys(channels).indexOf(cometId) !== -1
);

const getCometChannel = (cId) => {
    const cometId = cId || getMainCometId();
    if (!hasCometChannel(cometId)) {
        channels[cometId] = new Comet(cometId);
    }
    return channels[cometId];
};

export default Comet;

export {
    buildParams as _buildParams,
    CONNECTION_LOST,
    getCometChannel,
    getSubscriptions,
    getSubscriptionStatus as _getSubscriptionStatus,
    hasStopped as _hasStopped,
    notifyCallbacks,
    INVALID_SESSION_ID,
    getMainCometId,
    startSubscriptionFunc as startSubscription,
};
