import React from 'react';
import PropTypes from 'prop-types';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import Modal from './Modal';
import style from './ModalConfirmStyles';

export const isTextArea = node => node.nodeName.toLowerCase() === 'textarea';

class ModalConfirm extends React.Component {
    constructor(props) {
        super(props);

        this.onConfirm = this.onConfirm.bind(this);
        this.onCancel = this.onCancel.bind(this);
        this.onKeyDown = this.onKeyDown.bind(this);
        this.state = { focused: undefined };
    }

    onCancel() {
        this.props.onCancel();
        this.props.onRequestClose();
    }

    onConfirm() {
        this.props.onConfirm();
        if (this.props.closeOnConfirm) {
            this.props.onRequestClose();
        }
    }

    onKeyDown(e, valid, focusedElement) {
        if (e.key === 'Enter' && valid && !focusedElement && !isTextArea(e.target)) {
            this.onConfirm();
        }
        if (e.key === 'Esc') this.onCancel();
        if (e.key === 'Tab' && !e.shiftKey) {
            // TODO fix proper focus handling ENG-18881
            if (focusedElement === 2) e.preventDefault();
        }
    }

    render() {
        const {
            children,
            formIsValid,
            confirmTitle,
            preventAutofocus,
        } = this.props;
        const { focused } = this.state;
        const modalProps = { ...this.props, onRequestClose: this.onCancel };
        /* eslint-disable jsx-a11y/no-static-element-interactions */
        return (
            <div onKeyDown={e => this.onKeyDown(e, formIsValid, focused)}>
                <Modal {...modalProps}>
                    {children}
                    <div data-handle="modal-footer" style={style.footer}>
                        <button
                            autoFocus={!preventAutofocus}
                            data-handle="cancelBtn"
                            key="cancel"
                            style={defaultStyles.button2}
                            onClick={this.onCancel}
                            onFocus={() => this.setState({ focused: 1 })}
                            onBlur={() => this.setState({ preventClose: undefined, focused: 0 })}
                        >cancel</button>
                        <div style={{ width: '1rem' }} />
                        <button
                            data-handle="confirmBtn"
                            key="confirm"
                            disabled={!formIsValid}
                            style={formIsValid ?
                                defaultStyles.button : defaultStyles.buttonDisabled}
                            onClick={this.onConfirm}
                            onFocus={() => this.setState({ focused: 2 })}
                            onBlur={() => this.setState({ preventClose: undefined, focused: 0 })}
                        >{confirmTitle}</button>
                    </div>
                </Modal>
            </div>
        /* eslint-enable jsx-a11y/no-static-element-interactions */
        );
    }
}

ModalConfirm.propTypes = {
    children: PropTypes.node,
    closeOnConfirm: PropTypes.bool,
    confirmTitle: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.element,
    ]),
    onRequestClose: PropTypes.func,
    onConfirm: PropTypes.func,
    onCancel: PropTypes.func,
    preventAutofocus: PropTypes.bool,
    formIsValid: PropTypes.bool,
};

ModalConfirm.defaultProps = {
    closeOnConfirm: true,
    confirmTitle: 'confirm',
    onCancel: () => null,
    onConfirm: () => null,
    onRequestClose: () => null,
    preventAutofocus: false,
    shouldRender: true,
};

export default ModalConfirm;
