import { defaultLocale } from './defines';
import defaultTranslation from './en_trans';

const loadedTranslations = {};

const addTranslation = (language, messages) => {
    loadedTranslations[language] = messages;
    return { language, translation: messages };
};

const hasTranslation = language => (
    Object.prototype.hasOwnProperty.call(loadedTranslations, language)
);

const getTranslation = language => (
    loadedTranslations[language]
);

const removeTranslation = (language) => {
    delete loadedTranslations[language];
};

const clearTranslations = () => {
    const keys = Object.keys(loadedTranslations);
    keys.forEach((key) => { delete loadedTranslations[key]; });
};

const addDefaultTranslation = () => (
    addTranslation(defaultLocale, defaultTranslation)
);

const resetTranslation = () => {
    clearTranslations();
    return addDefaultTranslation();
};

addDefaultTranslation();

export {
    addTranslation,
    clearTranslations,
    defaultTranslation as _defaultTranslation,
    getTranslation,
    hasTranslation,
    removeTranslation,
    resetTranslation,
    loadedTranslations as _loadedTranslations,
};
