import jsonrpcSimple from '@webui-one/core/jsonrpc';
import { getSystemSetting } from 'modules/systemSetting/actions';
import { read as _read } from 'modules/transactions/actions';
import { isNonEmptyArray } from '@webui-one/core/utils/array';
import { delay } from '@webui-one/core/utils/async';
import * as actionTypes from './actionTypes';
import { selectModels, selectRootModelsLoading } from './reducer';

export const getModelSchema = (
    th,
    {
        namespace,
        name,
        funcs: {
            jsonrpc = jsonrpcSimple,
        } = {},
    } = {},
) => (
    jsonrpc('get_schema', {
        th,
        namespace,
        levels: 1,
        evaluate_when_entries: true,
    }).then(schema => ({ name, schema }))
);

export const getModelsCompleteAction = models =>
    ({ type: actionTypes.GET_MODELS_COMPLETE, models });
export const getModelsStartAction = () => ({ type: actionTypes.GET_MODELS_START });
export const getModelsErrorAction = error => ({ type: actionTypes.GET_MODELS_ERROR, error });
export const setModelExpandedAction = (modelName, expanded) =>
    ({ type: actionTypes.SET_MODEL_EXPANDED, modelName, expanded });

export const getModelsThunk = ({
    funcs: {
        read = _read,
        getModelSchemaFunc = getModelSchema,
    } = {},
} = {}) => (
    dispatch => (
        dispatch(getSystemSetting()).then((res) => {
            if (res === undefined) {
                return delay(500)().then(() => {
                    dispatch(getModelsThunk());
                });
            }

            dispatch(getModelsStartAction());
            const models = res.systemSetting.models;
            return dispatch(read()).then(({ th }) => {
                const promises = models.map(m => getModelSchemaFunc(th, m));

                return Promise.all(promises).then(promisesRes => (
                    dispatch(getModelsCompleteAction(promisesRes))
                ));
            });
        }).catch(error => dispatch(getModelsErrorAction(error)))
    )
);

export const maybeGetModels = ({
    funcs: {
        getModels = getModelsThunk,
    } = {},
} = {}) => (
    (dispatch, getState) => {
        const state = getState();
        const models = selectModels(state);

        if (selectRootModelsLoading(state) || isNonEmptyArray(models)) {
            return Promise.resolve();
        }

        return dispatch(getModels());
    }
);
