import { ensureArray } from '@webui-one/core/utils/array';
import { isNonEmptyString } from '@webui-one/core/utils/string';
import { isObject } from '@webui-one/core/utils/object';
import { isFunction } from '@webui-one/core/utils/functional';
import * as actionTypes from './actionTypes';

const findSubscription = (handler, path) => sub => (
    sub.handler === handler && sub.path === path
);

const getPathOptions = (path) => {
    if (isObject(path)) {
        return { path: path.path, isOperdata: path.isOperdata || false };
    }
    return { path, isOperdata: false };
};

const isValidPath = path => (
    isNonEmptyString(path) &&
    path !== '/' &&
    path.startsWith('/')
);

const subscribeChanges = (state, paths, handler) => {
    if (!isFunction(handler)) { return state; }

    const currentSubscriptions = ensureArray(state.changesHandlers);

    const subscribePaths = Array.isArray(paths) ? paths : [paths];
    const changesHandlers = subscribePaths.reduce((acc, p) => {
        const { path } = getPathOptions(p);
        if (isValidPath(path) && !acc.find(findSubscription(handler, path))) {
            acc.push({ path, handler });
        }
        return acc;
    }, currentSubscriptions.slice());
    if (changesHandlers.length === currentSubscriptions.length) {
        return state;
    }
    return {
        ...state,
        changesHandlers,
    };
};

const unsubscribeChanges = (state, paths, handler) => {
    if (!isFunction(handler)) { return state; }

    const currentSubscriptions = ensureArray(state.changesHandlers);

    const subscribePaths = Array.isArray(paths) ? paths : [paths];
    const changesHandlers = subscribePaths.reduce((acc, path) => {
        const index = acc.findIndex(findSubscription(handler, path));
        if (index !== -1) { acc.splice(index, 1); }
        return acc;
    }, currentSubscriptions.slice());
    if (changesHandlers.length === currentSubscriptions.length) {
        return state;
    }
    return {
        ...state,
        changesHandlers,
    };
};

const subscribeCdbOper = (state, paths, handler) => {
    if (!isFunction(handler)) { return state; }

    const currentSubscriptions = ensureArray(state.cdboperHandlers);

    const subscribePaths = Array.isArray(paths) ? paths : [paths];
    const cdboperHandlers = subscribePaths.reduce((acc, p) => {
        const { path } = getPathOptions(p);
        if (isValidPath(path) && !acc.find(findSubscription(handler, path))) {
            acc.push({ path, handler });
        }
        return acc;
    }, currentSubscriptions.slice());
    if (cdboperHandlers.length === currentSubscriptions.length) {
        return state;
    }
    return {
        ...state,
        cdboperHandlers,
    };
};

const unsubscribeCdbOper = (state, paths, handler) => {
    if (!isFunction(handler)) { return state; }

    const currentSubscriptions = ensureArray(state.cdboperHandlers);

    const subscribePaths = Array.isArray(paths) ? paths : [paths];
    const cdboperHandlers = subscribePaths.reduce((acc, path) => {
        const index = acc.findIndex(findSubscription(handler, path));
        if (index !== -1) { acc.splice(index, 1); }
        return acc;
    }, currentSubscriptions.slice());
    if (cdboperHandlers.length === currentSubscriptions.length) {
        return state;
    }
    return {
        ...state,
        cdboperHandlers,
    };
};

const subscribeMessageHandler = (state, handler) => {
    if (!isFunction(handler)) { return state; }

    const currentHandlers = ensureArray(state.messageHandlers);

    if (currentHandlers.indexOf(handler) !== -1) {
        return state;
    }
    return {
        ...state,
        messageHandlers: [
            ...currentHandlers,
            handler,
        ],
    };
};

const unsubscribeMessageHandler = (state, handler) => {
    const currentHandlers = ensureArray(state.messageHandlers);

    if (currentHandlers.indexOf(handler) === -1) {
        return state;
    }
    const messageHandlers = currentHandlers.reduce((acc, messageHandler) => {
        if (handler !== messageHandler) {
            acc.push(messageHandler);
        }
        return acc;
    }, []);
    return {
        ...state,
        messageHandlers,
    };
};
const subscribeUpgradeHandler = (state, handler) => {
    if (!isFunction(handler)) { return state; }

    const currentHandlers = ensureArray(state.upgradeHandlers);

    if (currentHandlers.indexOf(handler) !== -1) {
        return state;
    }
    return {
        ...state,
        upgradeHandlers: [
            ...currentHandlers,
            handler,
        ],
    };
};

const unsubscribeUpgradeHandler = (state, handler) => {
    const currentHandlers = ensureArray(state.upgradeHandlers);

    if (currentHandlers.indexOf(handler) === -1) {
        return state;
    }
    const upgradeHandlers = currentHandlers.reduce((acc, upgradeHandler) => {
        if (handler !== upgradeHandler) {
            acc.push(upgradeHandler);
        }
        return acc;
    }, []);
    return {
        ...state,
        upgradeHandlers,
    };
};

const initialState = {
    cdboperHandlers: [],
    changesHandlers: [],
    messageHandlers: [],
    upgradeHandlers: [],
};

export default function comet(state = initialState, action) {
    switch (action.type) {
    case actionTypes.SUBSCRIBE_CDB_OPER:
        return subscribeCdbOper(state, action.paths, action.handler);
    case actionTypes.UNSUBSCRIBE_CDB_OPER:
        return unsubscribeCdbOper(state, action.paths, action.handler);
    case actionTypes.SUBSCRIBE_CHANGES:
        return subscribeChanges(state, action.paths, action.handler);
    case actionTypes.UNSUBSCRIBE_CHANGES:
        return unsubscribeChanges(state, action.paths, action.handler);
    case actionTypes.SUBSCRIBE_MESSAGE_HANDLER:
        return subscribeMessageHandler(state, action.handler);
    case actionTypes.UNSUBSCRIBE_MESSAGE_HANDLER:
        return unsubscribeMessageHandler(state, action.handler);
    case actionTypes.SUBSCRIBE_UPGRADE_HANDLER:
        return subscribeUpgradeHandler(state, action.handler);
    case actionTypes.UNSUBSCRIBE_UPGRADE_HANDLER:
        return unsubscribeUpgradeHandler(state, action.handler);
    default:
        return state;
    }
}
