import {
    create as _create,
    deleteNode as _deleteNode,
    exists as _exists,
    getCase as _getCase,
    getLastTh as _getLastTh,
} from '@webui-one/core/jsonrpc';

import {
    getLeafName,
    getParentPath,
} from '@webui-one/core/utils/path';

import {
    getSchema as _getSchema,
    reEvaluateSchemaThunk as _reEvaluateSchemaThunk,
} from 'modules/schemas/actions';

import {
    read as _readThunk,
    write as _writeThunk,
} from 'modules/transactions/actions';

import {
    createAction,
    createErrorAction,
    deleteAction,
    deleteErrorAction,
    deleteNodeAndChildrenAction,
    existsAction,
    existsErrorAction,
    existsPendingAction,
    getCaseStartAction,
    getCaseCompleteAction,
    getCaseErrorAction,
    getValueCached as _getValueCached,
    setCaseCompleteAction,
    setValue as _setValue,
} from './actions';

export const existsThunk = ({
    exists,
    path,
    isActionInput,
    existsPath,
    funcs: {
        readThunk = _readThunk,
        existsFunc = _exists,
    } = {},
} = {}) => async (dispatch) => {
    if (isActionInput) {
        await dispatch(existsAction(path, { exists }));
        return Promise.resolve(undefined);
    }

    dispatch(existsPendingAction(existsPath || path));

    try {
        const { th } = await dispatch(readThunk({ path }));
        const res = await existsFunc({ th, path: existsPath || path });
        return dispatch(existsAction(path, res));
    } catch(error) {
        return dispatch(existsErrorAction(path, error));
    }
};

const internalCreateThunk = ({
    path,
    funcs: {
        create = _create,
        writeThunk = _writeThunk,
    } = {},
} = {}) => async (dispatch) => {
    const { th } = await dispatch(writeThunk({ path }));

    try {
        await create({ th, path });
        return dispatch(createAction(path));
    } catch(error) {
        return dispatch(createErrorAction(path, error));
    }
};

export const createAllowExistsThunk = ({
    path,
    funcs: {
        exists = existsThunk,
        create = internalCreateThunk,
    } = {},
} = {}) => async (dispatch) => {
    const res = await dispatch(exists({ path }));
    if (!res.exists) {
        return dispatch(create({ path }));
    }
    return Promise.resolve();
};

export const createDisallowExistsThunk = ({
    path,
    funcs: {
        create = internalCreateThunk,
        existsFunc = _exists,
        readThunk = _readThunk,
    } = {},
} = {}) => async (dispatch) => {
    try {
        const { th } = await dispatch(readThunk());
        const res = await existsFunc({ th, path });
        if (!res.exists) {
            return dispatch(create({ path }));
        }
        return { error: `The element ${path} already exists.` };
    } catch (error) {
        return { error: error.message };
    }
};

export const createThunk = ({
    caseAncestors,
    dependants,
    path,
    funcs: {
        createAllowExists = createAllowExistsThunk,
        reEvaluateSchemaThunk = _reEvaluateSchemaThunk,
    } = {},
} = {}) => async (dispatch) => {
    await dispatch(createAllowExists({ path }));

    if (dependants) {
        await dispatch(reEvaluateSchemaThunk(dependants));
    }

    if (caseAncestors) {
        caseAncestors.forEach(ca => (
            dispatch(setCaseCompleteAction(getParentPath(ca), getLeafName(ca)))
        ));
    }
};

export const deleteNodeThunk = ({
    path,
    deleteChildren,
    funcs: {
        deleteNode = _deleteNode,
        writeThunk = _writeThunk,
    } = {},
} = {}) => async (dispatch) => {
    const { th } = await dispatch(writeThunk({ path }));

    try {
        const res = await deleteNode({ th, path });
        if (res && res.warnings) throw new Error(res.warnings.join(' '));
        if (!deleteChildren) return dispatch(deleteAction(path));
        return dispatch(deleteNodeAndChildrenAction(path));
    } catch(error) {
        return dispatch(deleteErrorAction(path, error));
    }
};

export const deleteNodesThunk = ({
    paths,
} = {}) => dispatch => (
    Promise.all(
        paths.map(path => dispatch(deleteNodeThunk({ path }))),
    )
);

export const deleteLocalNodesThunk = ({
    paths,
    isActionInput,
} = {}) => dispatch => (
    Promise.all(
        paths.map(path => dispatch(deleteAction(path, isActionInput))),
    )
);

export const deleteAllowNonExistsThunk = ({
    path,
    deleteChildren,
    funcs: {
        exists = existsThunk,
        deleteNode = deleteNodeThunk,
    } = {},
} = {}) => async (dispatch) => {
    const res = await dispatch(exists({ path }));
    if (res.exists) return dispatch(deleteNode({ path, deleteChildren }));
    return Promise.resolve();
};

export const deleteThunk = ({
    caseAncestors,
    deleteChildren,
    dependants,
    path,
    funcs: {
        reEvaluateSchemaThunk = _reEvaluateSchemaThunk,
        deleteAllowNonExists = deleteAllowNonExistsThunk,
    } = {},
} = {}) => async (dispatch) => {
    await dispatch(deleteAllowNonExists({ path, deleteChildren }));

    if (dependants) {
        await dispatch(reEvaluateSchemaThunk(dependants));
    }

    if (caseAncestors) {
        caseAncestors.forEach(ca => (
            dispatch(setCaseCompleteAction(getParentPath(ca), getLeafName(ca)))
        ));
    }
};

const parentPath = path => path.substring(0, path.lastIndexOf('/'));

export const getCaseThunk = ({
    schemapath,
    nodepath,
    choice,
    funcs: {
        getCase = _getCase,
        getLastTh = _getLastTh,
    } = {},
} = {}) => (
    async (dispatch) => {
        const path = parentPath(nodepath || schemapath);
        const choicePath = `${path}/${choice}`;
        dispatch(getCaseStartAction(choicePath));
        try {
            const th = await getLastTh({ path });
            const result = await getCase({ th, path, choice });
            return dispatch(getCaseCompleteAction(choicePath, result.case));
        } catch(error) {
            return dispatch(getCaseErrorAction(choicePath, error));
        }
    }
);

export const getValueThunk = ({
    isConfigFalseCallpoint,
    path,
    funcs: {
        getValueCached = _getValueCached,
    } = {},
} = {}) => async (dispatch) => {
    await dispatch(getValueCached(path, isConfigFalseCallpoint));
};

export const setCaseThunk = ({
    path,
    parentNodepath,
    value,
    funcs: {
        getSchema = _getSchema,
    } = {},
} = {}) => async (dispatch) => {
    await dispatch(getSchema({ path: parentNodepath }));
    await dispatch(setCaseCompleteAction(path, value));
};

export const setValueThunk = ({
    caseAncestors,
    dependants,
    path,
    value,
    funcs: {
        reEvaluateSchemaThunk = _reEvaluateSchemaThunk,
        setValue = _setValue,
    } = {},
} = {}) => async (dispatch) => {
    await dispatch(setValue(path, value));

    if (dependants) {
        await dispatch(reEvaluateSchemaThunk(dependants));
    }

    if (caseAncestors) {
        caseAncestors.forEach(ca => (
            dispatch(setCaseCompleteAction(getParentPath(ca), getLeafName(ca)))
        ));
    }
};
