import actionTypes from './actionTypes';


const initialState = {
    errors: {},
    selected: undefined,
    shouldIgnoreErrors: false,
    hasNewErrors: false,
};

export default function errors(state = initialState, action) {
    const { payload } = action;

    switch (action.type) {
    case actionTypes.ERROR_REPORT_ADD:
        if (state.shouldIgnoreErrors) return state;
        return {
            ...state,
            hasNewErrors: true,
            errors: {
                ...state.errors,
                [payload.id]: {
                    ...payload,
                },
            },
        };
    case actionTypes.ERROR_REPORT_READ: {
        const { id } = payload;
        return {
            ...state,
            selected: id,
            errors: {
                ...state.errors,
                [id]: {
                    ...state.errors[id],
                    isRead: true,
                },
            },
        };
    }
    case actionTypes.ERROR_REPORT_RESET:
        return initialState;
    case actionTypes.ERROR_REPORT_PRUNE: {
        const { errors: currentErrors } = state;
        const errs = Object.keys(currentErrors).reduce((acc, eKey) => {
            const e = currentErrors[eKey];
            if (e.isRead) return acc;
            return {
                ...acc,
                [e.id]: e,
            };
        }, {});

        return {
            ...state,
            hasNewErrors: false,
            errors: errs,
        };
    }
    case actionTypes.ERROR_REPORT_SET_IGNORE: {
        return {
            ...state,
            ...payload,
        };
    }
    default:
        return state;
    }
}
