import * as actionTypes from './actionTypes';

const initialState = {
    isLoading: false,
    error: undefined,
};

const globalSettingsReducer = (state = initialState, action) => {
    switch (action.type) {
    case actionTypes.GET_GLOBAL_APPLICATION_SETTINGS_START: {
        return {
            ...state,
            isLoading: true,
            error: undefined,
        };
    }
    case actionTypes.GET_GLOBAL_APPLICATION_SETTINGS_COMPLETE: {
        return {
            ...state,
            isLoading: false,
            error: undefined,
            [action.setting]: action.value,
        };
    }
    case actionTypes.GET_GLOBAL_APPLICATION_SETTINGS_ERROR: {
        return {
            ...state,
            isLoading: false,
            error: action.error,
        };
    }
    default:
        return state;
    }
};

export default globalSettingsReducer;
