import React from 'react';
import PropTypes from 'prop-types';
import style from './ModalStyles';

class Modal extends React.Component {
    constructor(props) {
        super(props);

        this.onClose = this.onClose.bind(this);
        this.onKeyDown = this.onKeyDown.bind(this);
        this.modalRef = React.createRef();
        this.state = {
            isLargerThanScreen: false,
        };
    }

    componentDidMount() {
        window.addEventListener('keydown', this.onKeyDown);
        this.checkModalSize();
    }

    componentDidUpdate() {
        if (!this.state.isLargerThanScreen) this.checkModalSize();
    }

    componentWillUnmount() {
        window.removeEventListener('keydown', this.onKeyDown);
    }

    onKeyDown(event) {
        if (this.props.shouldRender && event.key === 'Escape') {
            this.onClose();
        }
    }

    onClose() {
        this.props.onRequestClose();
    }

    checkModalSize() {
        if (!this.modalRef.current) return;
        const sizeInRelationToWindow =
            (this.modalRef.current.offsetHeight / window.innerHeight) * 100;
        if (sizeInRelationToWindow > 80) {
            this.setState({ isLargerThanScreen: true });
        }
    }

    render() {
        const {
            isLargerThanScreen,
        } = this.state;
        const {
            children,
            title,
            shouldRender,
        } = this.props;
        /* eslint-disable jsx-a11y/no-static-element-interactions */
        return (
            <span>
                {shouldRender &&
                <div onKeyDown={this.onKeyDown} style={style.overlay}>
                    <button
                        data-handle="modalOverlay"
                        style={style.overlayClose}
                        onClick={this.onClose}
                    />
                    <div
                        data-handle="modal"
                        style={isLargerThanScreen ? { ...style.modalBox, overflow: 'auto' } : style.modalBox}
                        ref={this.modalRef}
                    >
                        <div data-handle="modal-title" style={style.title}>{title}</div>
                        <div data-handle="modal-body" style={style.body}>
                            {children}
                        </div>
                    </div>
                </div>
                }
            </span>);
        /* eslint-enable jsx-a11y/no-static-element-interactions */
    }
}

Modal.propTypes = {
    title: PropTypes.string,
    children: PropTypes.node,
    onRequestClose: PropTypes.func,
    shouldRender: PropTypes.bool,
};

Modal.defaultProps = {
    onRequestClose: () => null,
    shouldRender: true,
};

export default Modal;
