import * as actionTypes from 'modules/breadcrumbs/actionTypes';
import { getNameRelativeToParent } from '@webui-one/core/keypath/kp-parser';
import { quoteKey } from '@webui-one/core/functional_utils';

const initialState = { childrenNames: [], isLoading: false, isList: false, path: '' };

const getChildrenNamesStart = state => ({
    ...state, isLoading: true, childrenNames: [],
});

const createChildFilter = (showActions, showOperdata) => child => (
    !child.is_action_output && (
        child.kind === 'container' ||
        child.kind === 'list' ||
        (child.kind === 'action' && showActions)
    ) && (
        child.config !== false || showOperdata
    ) && (
        (
            child.evaluated_when_entry === undefined ||
            child.evaluated_when_entry === true
        ) && (
            child.evaluated_when_entry !== false
        )
    )
);

const parseChildrenNames = (path, schema, showActions, showOperdata) => {
    const childrenNames = schema.children.filter(
        createChildFilter(showActions, showOperdata),
    ).map(
        (child) => {
            const name = getNameRelativeToParent(path)(child);
            return name;
        });
    return childrenNames;
};

const getChildrenNamesComplete = (state, action) => {
    let childrenNames = [];
    if (!action.isList) {
        childrenNames = parseChildrenNames(
            action.path,
            action.result.data,
            action.showActions,
            action.showOperdata,
        );
    } else {
        childrenNames = action.result.results.map(result =>
            result.map(r => quoteKey(r)).join(' '),
        );
    }
    return {
        ...state,
        childrenNames,
        path: action.path,
        isLoading: false,
        isList: action.isList,
        schema: action.schema,
    };
};

export default function reducer(state = initialState, action) {
    switch (action.type) {
    case actionTypes.GET_CHILDREN_NAMES_START:
        return getChildrenNamesStart(state, action);
    case actionTypes.GET_CHILDREN_NAMES_COMPLETE:
        return getChildrenNamesComplete(state, action);
    case actionTypes.RESET:
        return { ...state, path: '' };
    case actionTypes.GET_CHILDREN_NAMES_ERROR:
        return state;
    default:
        return state;
    }
}

export {
    parseChildrenNames as _parseChildrenNames,
    getChildrenNamesComplete as _getChildrenNamesComplete,
};
