import React from 'react';
import PropTypes from 'prop-types';
import styles from './LoaderStyles.css';

const Loader = ({ color, extraStyle }) => {
    const eStyle = extraStyle || {};

    return (<div data-handle="loader">
        <span className={`${styles.dot} ${styles.dot1}`} style={{ backgroundColor: color, ...eStyle }} />
        <span className={`${styles.dot} ${styles.dot2}`} style={{ backgroundColor: color, ...eStyle }} />
        <span className={styles.dot} style={{ backgroundColor: color, ...eStyle }} />
    </div>);
};

Loader.propTypes = {
    color: PropTypes.string,
    extraStyle: PropTypes.shape({}),
};

export default Loader;
