import jsonrpc, {
    deleteTrans as _deleteTrans,
    getTrans as _getTrans,
    newTrans as _newTrans,
    showConfigJson2 as _showConfig,
} from '@webui-one/core/jsonrpc';

import {
    DBS,
    MODES,
} from '@webui-one/core/jsonrpc/constants';

import {
    getDataThunk as _getDataThunk,
} from 'modules/data/actions';

import {
    deleteTrans as _deleteTransThunk,
    read,
    write,
} from 'modules/transactions/actions';

import { sequence } from '@webui-one/core/utils/promise';
import { getTimestamp } from '@webui-one/core/utils/time';
import Messages from 'modules/intl/messages';
import { translate } from 'modules/intl/format';

import { doConfirm } from 'Common/Confirm/actions';

import * as actionTypes from './actionTypes';

export const runActionAbortAction = (path, getTimestampFun = getTimestamp) => ({
    type: actionTypes.RUN_ACTION_ABORT,
    completedAt: getTimestampFun(),
    path,
});

export const runActionCompleteAction = ({
    newTh,
    oldTh,
    path,
    result,
    getTimestampFun = getTimestamp,
}) => ({
    type: actionTypes.RUN_ACTION_COMPLETE,
    completedAt: getTimestampFun(),
    newTh,
    oldTh,
    path,
    result,
});

export const runCustomActionCompleteAction = (path, result, getTimestampFun = getTimestamp) => ({
    type: actionTypes.RUN_CUSTOM_ACTION_COMPLETE,
    completedAt: getTimestampFun(),
    path,
    result,
});


export const runActionErrorAction = (path, error, getTimestampFun = getTimestamp) => ({
    type: actionTypes.RUN_ACTION_ERROR,
    completedAt: getTimestampFun(),
    path,
    error,
});

export const runActionStartAction = (path, getTimestampFun = getTimestamp) => ({
    type: actionTypes.RUN_ACTION_START,
    startedAt: getTimestampFun(),
    completedAt: undefined,
    path,
});

export const clearActionOutputAction = path => ({
    type: actionTypes.CLEAR_ACTION_OUTPUT,
    path,
});

export const deleteConfigTransactions = ({
    funcs: {
        getTrans = _getTrans,
        deleteTransThunk = _deleteTransThunk,
    } = {},
} = {}) => async (dispatch) => {
    const result = await getTrans();
    const ths = result.trans.reduce((acc, cur) => {
        if (cur.mode === MODES.READ_WRITE && cur.db === DBS.RUNNING) {
            acc.push(cur.th);
        }
        return acc;
    }, []);

    const promises = ths.map(th => () => dispatch(deleteTransThunk(th)));
    await sequence(promises);
};


const refreshThunk = ({
    path,
    funcs: {
        getDataThunk = _getDataThunk,
    } = {},
} = {}) => dispatch => dispatch(getDataThunk({ path }));

export const runReadOnlyAction = (path, params) => (
    dispatch => (
        dispatch(deleteConfigTransactions()).then(() => {
            dispatch(runActionStartAction(path));
            return dispatch(read({ path })).then(({ th }) => (
                jsonrpc('run_action', { path, th, params })
            )).then((result) => {
                dispatch(runCustomActionCompleteAction(path, result));
                dispatch(refreshThunk({ path }));
            }).catch(error => (
                dispatch(runActionErrorAction(path, error))
            ));
        })
    )
);

const packagesReloadActionThunk = ({ path, params }) => dispatch => (
    dispatch(doConfirm(
        translate(Messages.CONFIGURATIONEDITOR.RELOAD_PACKAGES_MESSAGE),
        translate(Messages.CONFIGURATIONEDITOR.RELOAD_PACKAGES_TITLE),
    )).then((status) => {
        if (status === 0) {
            return Promise.resolve(runActionAbortAction(path));
        }
        return dispatch(runReadOnlyAction(path, params));
    })
);

const getApplyTemplateParams = async ({
    th,
    path,
    params = {},
    funcs: {
        showConfig = _showConfig,
    } = {},
} = {}) => {
    const templateParams = {};
    const config = await showConfig({ th, path });

    const applyTemplate = config['ncs:apply-template'];
    if (applyTemplate) {
        if ('accept-empty-capabilities' in applyTemplate) {
            templateParams['accept-empty-capabilities'] = true;
        }
        if ('suppress-positive-result' in applyTemplate) {
            templateParams['suppress-positive-result'] = true;
        }
        if ('template-name' in applyTemplate) {
            templateParams['template-name'] = applyTemplate['template-name'];
        }
    }

    if (params.variable) {
        templateParams.variable = params.variable;
    }

    return templateParams;
};

const applyTemplateThunk = ({
    path,
    params,
    funcs: {
        writeThunk = write,
    } = {},
} = {}) => async (dispatch) => {
    dispatch(runActionStartAction(path));

    try {
        const { th } = await dispatch(writeThunk());
        const { th: actionTh } = await dispatch(writeThunk({ actionPath: path }));

        const templateParams = await getApplyTemplateParams({ path, th: actionTh, params });
        const result = await jsonrpc('run_action', { path, th, params: templateParams });

        dispatch(runCustomActionCompleteAction(path, result));
        dispatch(refreshThunk({ path }));
    } catch(error) {
        dispatch(runActionErrorAction(path, error));
    }
};

const reDeployDryRunActionThunk = ({
    executePath,
    path,
    funcs: {
        writeThunk = write,
    } = {},
}) => async (dispatch) => {
    dispatch(runActionStartAction(path));

    try {
        const { th } = await dispatch(writeThunk());
        const params = { 'dry-run': { outformat: 'cli' } };
        const result = await jsonrpc('run_action', { path: executePath, th, params });
        dispatch(runCustomActionCompleteAction(path, result));
    } catch(error) {
        dispatch(runActionErrorAction(path, error));
    }
};


const customActionFactory = ({ action, path }) => {
    if (action === 're-deploy dry-run') {
        return reDeployDryRunActionThunk;
    }

    if (path === '/ncs:packages/reload') {
        return packagesReloadActionThunk;
    }

    if (/^\/ncs:devices\/device\{[^}]+\}\/apply-template$/.test(path)) {
        return applyTemplateThunk;
    }
    return undefined;
};

export const runCustomActionThunk = ({
    action,
    path,
    params,
    executePath,
    funcs: {
        writeThunk = write,
    } = {},
} = {}) => async (dispatch) => {
    const customActionThunk = customActionFactory({ action, path });

    if (customActionThunk) {
        return dispatch(customActionThunk({ action, path, params, executePath }));
    }

    dispatch(runActionStartAction(path));
    try {
        const { th } = await dispatch(writeThunk());
        const result = await jsonrpc('run_action', { path: executePath || path, th });
        dispatch(runCustomActionCompleteAction(path, result));
    } catch(error) {
        dispatch(runActionErrorAction(path, error));
    }
    return undefined;
};


export const runActionThunk = ({
    path,
    shouldRefresh,
    funcs: {
        writeThunk = write,
        deleteTrans = _deleteTrans,
        newTrans = _newTrans,
    } = {},
} = {}) => async (dispatch) => {
    const customActionThunk = customActionFactory({ path });

    if (customActionThunk) {
        return dispatch(customActionThunk({ path }));
    }

    dispatch(runActionStartAction(path));
    try {
        const { th } = await dispatch(writeThunk({ path }));
        const result = await jsonrpc('run_action', { path, th });
        await deleteTrans({ th });
        const { th: newTh } = await newTrans({ actionPath: path });
        dispatch(runActionCompleteAction({ path, result, oldTh: th, newTh }));
        if (shouldRefresh) {
            await dispatch(refreshThunk({ path }));
        }
    } catch(error) {
        dispatch(runActionErrorAction(path, error));
    }
    return undefined;
};

export const loadActionAction = ({ path }) => ({
    type: actionTypes.LOAD_ACTION_ACTION,
    path,
});
