import { isDevEnv } from '@webui-one/core/utils/functional';
import {
    _defaultTranslation as defaultMessages,
    getTranslation,
} from './translation';
import { defaultLocale } from './defines';
import MessageFormatter from './MessageFormatter';

const formatters = {};

const getMessageFormatter = (template, locale) => {
    const pattern = `${template}___${locale}`;
    if (Object.prototype.hasOwnProperty.call(formatters, pattern)) {
        return formatters[pattern];
    }
    const formatter = new MessageFormatter(template, locale);
    formatters[pattern] = formatter;
    return formatter;
};

const hasTemplate = (language, message) => {
    const translation = getTranslation(language);
    return translation && translation[message];
};

const getTemplate = (language, message) => {
    if (hasTemplate(language, message)) {
        return getTranslation(language)[message];
    }
    if (typeof defaultMessages[message] === 'string') {
        return defaultMessages[message];
    }
    if (isDevEnv()) {
        // eslint-disable-next-line no-console
        console.log(`Missing message '${message}' in language '${language}'`);
    }
    return '';
};

const formatTemplate = (template, values, language) => {
    const formatter = getMessageFormatter(template, language);
    return formatter.format(values);
};

const translate = (message, values, language = defaultLocale) => {
    const template = getTemplate(language, message);
    return formatTemplate(template, values, language);
};

export {
    formatTemplate,
    getMessageFormatter,
    translate,
    getTemplate,
};
