
import {
    createSelector,
} from 'reselect';

export const selectError = state => state.error;
export const selectHasNewErrors = state => selectError(state).hasNewErrors;
export const selectShouldIngoreErrors = state => selectError(state).shouldIgnoreErrors;

export const selectErrors = state => selectError(state).errors;
export const selectErrorsKeys = createSelector(
    selectErrors,
    errors => Object.keys(errors).map(x => parseInt(x, 10)).sort((a, b) => a - b),
);

export const selectErrorsCount = createSelector(
    selectErrorsKeys,
    errorKeys => errorKeys.length,
);

export const selectCurrentErrorKey = state => selectError(state).selected;
export const selectCurrentError = state => selectErrors(state)[selectCurrentErrorKey(state)];
