import {
    selectUserMetadata,
} from 'modules/userdata/selectors';
import {
    METADATA_TAG,
    subscribePath,
    userMetadataPath,
    userProfileMetaDataPath,
} from 'modules/userdata/paths';
import {
    fetchStateFromCdb,
    parseUserdata,
    setUserdataAction,
    updateUserMetadata,
} from 'modules/userdata/actions';
import {
    takeLastFactory,
} from '@webui-one/core/utils/async';
import { selectNsoUser } from 'modules/systemSetting/reducer';
import { isDeepEqual } from '@webui-one/core/utils/object';
import * as nodesActionTypes from 'modules/nodes/actionTypes';
import * as transactionActionTypes from 'modules/transactions/actionTypes';
import { subscribeChangesAction } from 'modules/comet/actions';
import * as userdataActionTypes from './actionTypes';

const skip = [
    nodesActionTypes.GET_VALUE,
    userdataActionTypes.SET_USERDATA,
];


const fetchActions = [
    userdataActionTypes.FETCH_USERDATA_PENDING,
    userdataActionTypes.FETCH_USERDATA_END,
    userdataActionTypes.FETCH_USERDATA_ERROR,
];

const createHandlerFunc = store => (notifications) => {
    notifications.changes.forEach((change) => {
        const prevData = selectUserMetadata(store.getState());
        const user = selectNsoUser(store.getState());
        const userdataPath = userMetadataPath(user);
        const profilePath = userProfileMetaDataPath(user);
        let nextData;
        if (!user
            || (
                change.keypath !== userdataPath
                && change.keypath !== profilePath
            )
        ) {
            return;
        }
        if (change.op === 'deleted' || change.op === 'default_set') {
            nextData = {};
        }
        if (change.op === 'value_set') {
            nextData = parseUserdata(change.value);
        }
        if (!nextData || isDeepEqual(prevData, nextData)) {
            return;
        }
        store.dispatch(setUserdataAction(
            nextData,
            change.keypath,
        ));
    });
};

const createUserdataHandler = (userdataStatus, takeLastFun = takeLastFactory()) => {
    let handler;
    const middleware = ({ dispatch, getState }) => next => (action) => {
        const user = selectNsoUser(getState());
        if (fetchActions.indexOf(action.type) !== -1) {
            userdataStatus(action.type);
            return next(action, user);
        }

        if (userdataStatus() !== userdataActionTypes.FETCH_USERDATA_END) {
            const nextAction = next(action, user);
            if (userdataStatus() === undefined && user) {
                dispatch(fetchStateFromCdb(user, METADATA_TAG));
                dispatch(
                    subscribeChangesAction(
                        subscribePath(user),
                        handler,
                    ),
                );
            }
            return nextAction;
        }

        if (skip.indexOf(action.type) !== -1) {
            return next(action, user);
        }

        const prevData = selectUserMetadata(getState());
        const nextAction = next(action, user);
        const nextData = selectUserMetadata(getState());

        if (action.type === transactionActionTypes.DELETE_TRANS) {
            dispatch(fetchStateFromCdb(user, METADATA_TAG));
            return nextAction;
        }

        if (!isDeepEqual(prevData, nextData)) {
            const userdataPath = userMetadataPath(user);
            dispatch(setUserdataAction(nextData, userdataPath));
            takeLastFun(() => (
                dispatch(
                    updateUserMetadata(
                        user,
                        JSON.stringify(nextData),
                    ),
                )),
            );
        }
        return nextAction;
    };
    middleware.initialize = (store, { funcs: { createHandler = createHandlerFunc } = {} } = {}) => {
        handler = createHandler(store);
    };
    return middleware;
};

export default createUserdataHandler;
