import * as actionTypes from './actionTypes';
import defaultApplications from '@webui-one/core/defaultApplications';

const initialState = {
    applications: defaultApplications,
};

export default function applications(state = initialState, action) {
    switch (action.type) {
    case actionTypes.APPLICATIONS_GET_START:
        return {
            ...state,
            isLoading: true,
        };
    case actionTypes.APPLICATIONS_GET:
        return {
            ...state,
            isLoading: false,
            applications: {
                ...state.applications,
                ...action.applications,
            },
        };
    default:
        return state;
    }
}
