import { assignDeep, omitKeys } from '@webui-one/core/utils/object';
import * as dataActionTypes from 'modules/data/actionTypes';
import { loadStatus } from 'modules/schemas/reducer';
import * as servicesActionTypes from 'modules/services/actionTypes';

import * as actionTypes from './actionTypes';

const initialState = {
    staticSchemas: {},
    evaluatedSchemas: {},
};

const handleGetSchemasComplete = (state = initialState, action) => {
    const { path, schemas } = action;

    return {
        ...state,
        loading: {
            ...state.loading,
            [path]: { loadStatus: loadStatus.COMPLETE },
        },
        staticSchemas: assignDeep({}, state.staticSchemas, schemas.staticSchemas),
        evaluatedSchemas: assignDeep({}, state.evaluatedSchemas, schemas.evaluatedSchemas),
    };
};

const handleGetSchemasError = (state = initialState, action) => {
    const { path, errors } = action;

    return {
        ...state,
        errors: {
            ...state.errors,
            [path]: { loadStatus: loadStatus.ERROR, errors },
        },
    };
};

const handleGetSchemasStart = (state = initialState, action) => {
    const { path } = action;

    return {
        ...state,
        loading: {
            ...state.loading,
            [path]: { loadStatus: loadStatus.LOADING },
        },
    };
};

const handleReEvaluateSchema = (state, action) => {
    const schemaPath = action.path;
    return {
        ...state,
        reEvaluate: {
            [action.path]: [schemaPath, ...action.dependants],
        },
    };
};

const handleGetDataSchemaComplete = (state, action) => {
    const { path, schemas2 } = action;
    const { staticSchemas, evaluatedSchemas } = schemas2;

    return {
        ...state,
        staticSchemas: {
            ...state.staticSchemas,
            ...staticSchemas,
        },
        evaluatedSchemas: {
            ...state.evaluatedSchemas,
            ...evaluatedSchemas,
        },
        loading: {
            ...state.loading,
            [path]: { loadStatus: loadStatus.COMPLETE },
        },
    };
};

const handleGetDataSchemaStart = (state, action) => {
    const { path } = action;

    return {
        ...state,
        loading: {
            ...state.loading,
            [path]: { loadStatus: loadStatus.LOADING },
        },
    };
};

const handleGetServicePointSchemasComplete = (state, action) => {
    const stateStaticSchemas = state.staticSchemas || {};
    const actionStaticSchemas = action.schemas2.staticSchemas;
    const staticSchemas = omitKeys(actionStaticSchemas, Object.keys(stateStaticSchemas));
    const schemas2 = {
        ...actionStaticSchemas,
        ...action.schemas2,
        staticSchemas,
    };
    return handleGetDataSchemaComplete(state, { ...action, schemas2 });
};

const schemas = (state = initialState, action = {}) => {
    switch (action.type) {
    case actionTypes.GET_SCHEMAS_COMPLETE:
        return handleGetSchemasComplete(state, action);
    case actionTypes.GET_SCHEMAS_ERROR:
        return handleGetSchemasError(state, action);
    case actionTypes.GET_SCHEMAS_START:
        return handleGetSchemasStart(state, action);
    case actionTypes.REEVALUATE_SCHEMA:
        return handleReEvaluateSchema(state, action);
    case dataActionTypes.GET_DATA_START:
        return handleGetDataSchemaStart(state, action);
    case dataActionTypes.GET_DATA_COMPLETE:
        return handleGetDataSchemaComplete(state, action);
    case servicesActionTypes.GET_SERVICE_POINT_SCHEMAS_COMPLETE:
        return handleGetServicePointSchemasComplete(state, action);
    default:
        return state;
    }
};

export default schemas;

export {
    handleGetSchemasComplete as _handleGetSchemasComplete,
    handleGetSchemasStart as _handleGetSchemasStart,
    handleGetDataSchemaStart as _handleGetDataSchemaStart,
};
