import { connect } from 'react-redux';
import {
    pruneErrorsAction,
    readErrorAction,
    resetErrorsAction,
    setShouldIgnoreErrors,
} from 'modules/error/actions';

import {
    selectErrorsCount,
    selectErrorsKeys,
    selectCurrentError,
    selectCurrentErrorKey,
    selectHasNewErrors,
} from 'modules/error/selectors';

import ErrorHandler from './ErrorHandler';

const mapStateToProps = state => ({
    error: selectCurrentError(state),
    errorKeys: selectErrorsKeys(state),
    errorCount: selectErrorsCount(state),
    selected: selectCurrentErrorKey(state),
    hasNewErrors: selectHasNewErrors(state),
});

const mapDispatchToProps = dispatch => ({
    onReadError(errorId) {
        dispatch(readErrorAction(errorId));
    },
    onPruneErrors() {
        dispatch(pruneErrorsAction());
    },
    onResetErrorsAction() {
        dispatch(resetErrorsAction());
    },
    onSetIngoreErrors(shouldIngore) {
        dispatch(setShouldIgnoreErrors(shouldIngore));
    },
});

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(ErrorHandler);

export {
    mapDispatchToProps as _mapDispatchToProps,
    mapStateToProps as _mapStateToProps,
};
