import React from 'react';
import PropTypes from 'prop-types';
import { Messages, translate } from 'modules/intl';
import style from './ErrorPageStyles';

const ErrorPage = (props) => {
    const { routeParams } = props;
    return (
        <div style={style.application} id="errorPage">
            <div style={style.box}>
                <h1 style={style.header}> {translate(Messages.ERRORPAGE.ERROR_HEADER)}</h1>
                <span style={style.info}>
                    {translate(Messages.ERRORPAGE.ERROR_INFO, { path: routeParams.splat })}
                </span>
            </div>
        </div>);
};
export default ErrorPage;

ErrorPage.propTypes = {
    routeParams: PropTypes.shape(Object).isRequired,
};
