import * as actionTypes from './actionTypes';

const initialState = {
    alarms: {},
    summary: {},
};

const alarms = (state = initialState, action) => {
    switch (action.type) {
    case actionTypes.GET_ALARMS_START: {
        return {
            ...state,
            alarms: {
                ...state.alarms,
                isLoading: true,
            },
        };
    }
    case actionTypes.GET_ALARMS_COMPLETE: {
        return {
            ...state,
            alarms: {
                isLoading: false,
                ...action.alarmList,
            },
        };
    }
    case actionTypes.GET_ALARMS_ERROR: {
        return {
            ...state,
            alarms: {
                isLoading: false,
                ...action.error,
            },
        };
    }
    case actionTypes.GET_ALARM_SUMMARY_START: {
        return {
            ...state,
            summary: {
                ...state.summary,
                isLoading: true,
            },
        };
    }
    case actionTypes.GET_ALARM_SUMMARY_COMPLETE: {
        return {
            ...state,
            summary: {
                summary: action.values,
                isLoading: false,
            },
        };
    }
    case actionTypes.GET_ALARM_SUMMARY_ERROR: {
        return {
            ...state,
            summary: {
                ...action.error,
                isLoading: false,
            },
        };
    }
    default:
        return state;
    }
};

export default alarms;
