import {
    applyMiddleware,
    compose,
    createStore,
} from 'redux';
import thunk from 'redux-thunk';
import { initDevModeLogger } from 'core/devmode';
import createSideEffectMiddleware from 'core/sideEffectMiddleware';
import {
    loadStoredState,
    initStorage,
    setStorageTag,
} from '@webui-one/storage/storage';
import createIpcMiddleware from 'modules/ipc/middleware';
import createUserdataHandler from 'modules/userdata/middleware';
import { initApplication, getApplicationStorageTag } from 'modules/application/utils';
import { wrappedValue } from '@webui-one/core/functional_utils';
import { initFlags } from 'modules/flags';

import rootReducer from '../rootReducer';

import { getApplicationStorageFilters } from './storage-filters';

export const getReduxState = store => () => store.getState();

const STORAGE_METADATA_TAG = getApplicationStorageTag();

const configureStore = (initialState) => {
    const sideEffectMiddleware = createSideEffectMiddleware();
    const ipcMiddleware = createIpcMiddleware();
    const userdataMiddleware = createUserdataHandler(wrappedValue());

    const [
        middlewares,
        composeEnhancers,
    ] = initDevModeLogger([
        userdataMiddleware,
        ipcMiddleware,
        sideEffectMiddleware,
        thunk,
    ], compose);

    setStorageTag(STORAGE_METADATA_TAG);

    const store = createStore(
        rootReducer,
        loadStoredState(initialState),
        composeEnhancers(applyMiddleware(...middlewares)),
    );

    userdataMiddleware.initialize(store);
    ipcMiddleware.initialize(store);
    sideEffectMiddleware.initialize(store);
    initStorage(
        getReduxState(store),
        getApplicationStorageFilters,
        STORAGE_METADATA_TAG,
    );
    initFlags(store);
    initApplication(store);
    return store;
};

export {
    STORAGE_METADATA_TAG as _STORAGE_METADATA_TAG,
};

export default configureStore;
