import * as actionTypes from 'modules/nodes/actionTypes';
import * as dataActionTypes from 'modules/data/actionTypes';
import * as transactionActionTypes from 'modules/transactions/actionTypes';
import * as yangActionsActionTypes from 'modules/yangActions/actionTypes';
import { REEVALUATE_SCHEMA } from 'modules/schemas/actionTypes';
import {
    findCommonAncestor,
    pathToObjects,
    pathWithoutKeys,
} from '@webui-one/core/keypath/kp-parser';
import {
    quotePathKeys,
} from '@webui-one/core/utils/path';

const initialState = {
};

export const loadStatus = {
    LOADING: 'LOADING',
    ERROR: 'ERROR',
    COMPLETE: 'COMPLETE',
};

const isActionChild = (state, path) => (
    Object.keys(state).reduce((acc, cur) => {
        const val = state[cur];
        if (path.startsWith(cur) && val.isAction === true) return true;
        return acc;
    }, false)
);


const updateDirtyState = (state, path, isDirty = false, wasDirty = false) => {
    if (isActionChild(state, path)) {
        return state;
    }

    let diff = 0;
    if (isDirty && !wasDirty) {
        diff = 1;
    }

    if (!isDirty && wasDirty) {
        diff = -1;
    }

    if (diff !== 0) {
        return [{ parentPath: '/' }, ...pathToObjects(path)].reduce((accState, p) => {
            if (p.parentPath) {
                const refCountExists = state[p.parentPath] && state[p.parentPath].dirtyRefCount;
                const dirtyRefCount = refCountExists ? state[p.parentPath].dirtyRefCount : 0;
                const parentState = accState[p.parentPath];
                if (parentState && parentState.presence) {
                    return {
                        ...accState,
                        [p.parentPath]: {
                            ...accState[p.parentPath],
                            dirtyRefCount: dirtyRefCount + diff,
                            exists: true,
                        },
                    };
                }
                return {
                    ...accState,
                    [p.parentPath]: {
                        ...accState[p.parentPath],
                        dirtyRefCount: dirtyRefCount + diff,
                    },
                };
            }
            return accState;
        }, { ...state,
            [path]: {
                ...state[path],
                isDirty,
            },
        });
    }
    return state;
};

const handleChanges = (state, action) => {
    const { path } = action;
    const quotedPath = quotePathKeys(path);
    const node = quotedPath in state ? state[quotedPath] : {};
    const { isDirty: wasDirty, newValue, value } = node;
    const isDirty = (newValue !== undefined && value !== newValue);

    return updateDirtyState(state, quotedPath, isDirty, wasDirty);
};

const shouldNodeBeMarkedAsDeleted = (node, action) => {
    if (!('isDeleted' in node)) return false;
    if (node.isDeleted &&
       (transactionActionTypes.COMMIT_COMPLETE === action.type ||
        transactionActionTypes.GET_TRANS_CHANGES === action.type)) return true;
    return node.isDirty && !node.isDeleted && transactionActionTypes.DELETE_TRANS === action.type;
};

const handleTransactionChanges = (state, action) => (
    Object.keys(state).reduce((acc, path) => {
        const node = state[path];
        if (node.isDirty && node.newValue) {
            return { ...acc,
                [path]: {
                    ...node,
                    value: action.type === transactionActionTypes.COMMIT_COMPLETE ?
                        node.newValue : node.value,
                    newValue: undefined,
                    isDirty: false,
                },
            };
        }

        if (node.isDirty || node.dirtyRefCount) {
            const isDeleted = shouldNodeBeMarkedAsDeleted(node, action);
            return { ...acc,
                [path]: {
                    ...node,
                    dirtyRefCount: 0,
                    isDirty: false,
                    isDeleted,
                    exists: !isDeleted,
                },
            };
        }

        return acc;
    }, state)
);

const valueSetOperation = (state, change) => (
    { ...state,
        [change.path]: {
            ...state[change.path],
            newValue: change.value,
            value: change.old === '' ? undefined : change.old,
        },
    });


const valueDeleteOperation = (state, change) => (
    { ...state,
        [change.path]: {
            ...state[change.path],
            isDeleted: true,
            error: undefined,
        },
    });

const valueCreateOperation = (state, change) => {
    const quotedPath = quotePathKeys(change.path);
    return { ...state,
        [quotedPath]: {
            ...state[quotedPath],
            isDeleted: false,
            error: undefined,
        },
    };
};

const changeOperationMapper = {
    value_set: valueSetOperation,
    default_set: state => state,
    created: valueCreateOperation,
    deleted: valueDeleteOperation,
};

const canMapChangeOp = change => Object.keys(changeOperationMapper).indexOf(change.op) > -1;

const handleDiffState = (state, action) => action.changes
    .filter(canMapChangeOp)
    .reduce((accState, change) =>
        updateDirtyState(
            changeOperationMapper[change.op](accState, change),
            quotePathKeys(change.path),
            true,
            false,
        ), handleTransactionChanges(state, action));

export const selectNodes = state => state.nodes;

const presenceConfig = (presenceData) => {
    const config = { presence: true };
    if (presenceData && presenceData.exists) {
        config.exists = true;
    }
    return config;
};

const getRows = (state, path) => (state[path] || {}).rows || [];

const handleDelete = (state, action) => {
    const defaultValue = (state[action.path] || {}).default;
    return {
        ...state,
        [action.path]: {
            ...state[action.path],
            isDeleted: defaultValue === undefined,
            exists: defaultValue !== undefined,
            value: defaultValue,
            newValue: undefined,
            error: undefined,
        },
    };
};

const omitNodes = (state, path) => Object.keys(state).reduce((acc, n) => {
    if (!n.startsWith(`${path}/`)) {
        acc[n] = state[n];
    } else if (state[n].presence) {
        acc[n] = presenceConfig();
    }
    return acc;
}, {});

const handleDeleteNodeAndChildren = (state, action) => ({
    ...omitNodes(state, action.path),
    [action.path]: {
        ...state[action.path],
        isDeleted: true,
        exists: false,
    },
});

const reduceHadData = (node = {}, action) => {
    const hadData = node.hadData || {};
    const rows = action.result.results;
    if (rows.length < 1) return hadData;
    const keys = Object.keys(rows[0]);
    return keys.reduce((acc, key) => {
        if (acc[key]) return acc;
        return {
            ...acc,
            [key]: !rows.every(row => row[key] === undefined),
        };
    }, hadData);
};

const getKeypathFromCommonAncestor = (keypath, schemaPath) => {
    const commonAncestor = findCommonAncestor(keypath, schemaPath);
    if (!commonAncestor) return undefined;
    return schemaPath.replace(pathWithoutKeys(commonAncestor), commonAncestor);
};

const handleReEvaluateSchemaAction = (state, action) => {
    const { path, dependants } = action;
    if (!dependants || dependants.length === 0) return state;

    const reLoadPaths = dependants.map(p => getKeypathFromCommonAncestor(path, p));

    const updatedState = reLoadPaths.reduce((acc, cur) => {
        if (cur === path) return acc;
        acc[cur] = {
            ...state[cur],
            reloadValues: true,
        };
        return acc;
    }, {});
    return {
        ...state,
        ...updatedState,
    };
};

const handleModifiedNodes = (state, action) => {
    const newState = { ...state };
    action.paths.reduce((acc, path) => {
        const node = {
            ...acc[path],
            isModified: true,
        };
        acc[path] = node;
        return acc;
    }, newState);
    return newState;
};

const dataIsLeafList = data => (
    Array.isArray(data.rows) &&
    data.rows.length > 0 &&
    data.rows.length === data.totalNumberOfResults &&
    data.rows.every(r => Object.keys(r).length === 1 && r[0] !== undefined)
);

const handleDataComplete = (state, action) => {
    const newNodes = Object.entries(action.data).reduce((acc, [keypath, data]) => {
        if (dataIsLeafList(data)) {
            acc[keypath] = { ...data };
        }
        return acc;
    }, {});
    if (Object.keys(newNodes).length === 0) {
        return state;
    }
    return {
        ...state,
        ...newNodes,
    };
};

export default function nodes(state = initialState, action) {
    switch (action.type) {
    case actionTypes.MODIFIED_NODES:
        return handleModifiedNodes(state, action);
    case actionTypes.CHANGE_VALUE:
        return handleChanges({ ...state,
            [action.path]: {
                ...state[action.path],
                newValue: action.value,
                error: undefined,
            },
        }, action);
    case actionTypes.CREATE:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                value: action.value,
                exists: true,
                error: undefined,
                isDeleted: false,
                default: undefined,
            },
        };
    case actionTypes.DELETE:
        return handleDelete(state, action);
    case actionTypes.DELETE_NODE_AND_CHILDREN:
        return handleDeleteNodeAndChildren(state, action);
    case actionTypes.CURRENT_ROOT_NODE:
        return { ...state, currentRootPath: action.path };
    case actionTypes.EXISTS_PENDING:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                existsPending: true,
            },
        };
    case actionTypes.EXISTS_ERROR:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                existsPending: false,
            },
        };
    case actionTypes.EXISTS:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                exists: action.exists,
                existsPending: false,
            },
        };
    case actionTypes.GET_LEAFREF_VALUES_START:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                error: undefined,
                values: undefined,
                isLoading: true,
                reloadValues: undefined,
            },
        };
    case actionTypes.GET_LEAFREF_VALUES_ERROR:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                values: undefined,
                error: action.error,
                isLoading: false,
            },
        };
    case actionTypes.GET_LEAFREF_VALUES_COMPLETE:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                values: action.values,
                error: undefined,
                isLoading: false,
            },
        };
    case actionTypes.GET_VALUES_PENDING:
        return action.values.reduce((acc, value) => ({
            ...acc,
            [value.path]: {
                ...acc[value.path],
                isLoading: true,
                error: undefined,
            },
        }), state);
    case actionTypes.GET_VALUES:
        return action.values.reduce((acc, value) => ({
            ...acc,
            [value.path]: {
                ...acc[value.path],
                value: value.value,
                isLoading: false,
                error: value.error,
            },
        }), state);
    case actionTypes.GET_VALUES_ERROR:
        return action.values.reduce((acc, value) => ({
            ...acc,
            [value.path]: {
                ...acc[value.path],
                isLoading: false,
                error: value.error,
            },
        }), state);
    case actionTypes.GET_VALUE:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                value: action.value,
                isLoading: false,
                error: undefined,
            },
        };
    case actionTypes.GET_VALUE_ERROR:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                isLoading: false,
                error: action.error,
            },
        };
    case actionTypes.GET_VALUE_PENDING:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                isLoading: true,
            },
        };
    case actionTypes.HIDE:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                isHidden: action.value,
            },
        };
    case actionTypes.LOADING:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                loaded: false,
                loading: true,
            },
        };
    case actionTypes.LOADING_COMPLETE:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                loaded: true,
                loading: false,
            },
        };
    case actionTypes.SET_VALUE_START:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                isLoading: true,
                error: undefined,
            },
        };
    case actionTypes.SET_VALUE_COMPLETE:
        return handleChanges({ ...state,
            [action.path]: {
                ...state[action.path],
                newValue: action.value,
                isLoading: false,
                error: undefined,
            },
        }, action);
    case actionTypes.SET_VALUE_INTERRUPT:
        return handleChanges({ ...state,
            [action.path]: {
                ...state[action.path],
                isLoading: false,
            },
        }, action);
    case actionTypes.SET_LOCAL_VALUE:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                newValue: action.value,
                error: undefined,
            },
        };
    case actionTypes.SET_CHECKBOX_VALUE:
        return {
            ...state,
            [action.path]: {
                ...state[action.path],
                selectedRows: action.keyPaths.reduce((acc, keyPath) => ({
                    ...acc,
                    [keyPath]: action.value,
                }), state[action.path].selectedRows),
            },
        };
    case transactionActionTypes.COMMIT_COMPLETE:
    case transactionActionTypes.DELETE_TRANS:
        return handleTransactionChanges(state, action);
    case transactionActionTypes.GET_TRANS_CHANGES:
        return handleDiffState(state, action);
    case actionTypes.SET_VALUE_ERROR:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                isLoading: false,
                error: action.error,
            },
        };
    case actionTypes.QUERY:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                hadData: reduceHadData(state[action.path], action),
                error: undefined,
                isLoading: false,
                isModified: false,
                offset: action.result.current_position,
                rows: action.result.results,
                sort: action.sort,
                sortOrder: action.sortOrder,
                totalNumberOfResults: action.result.total_number_of_results,
            },
        };
    case actionTypes.QUERY_PENDING:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                error: undefined,
                isLoading: true,
            },
        };
    case actionTypes.QUERY_ERROR:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                error: action.error,
                isLoading: false,
            },
        };
    case actionTypes.GET_CASE_START:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                loadStatus: loadStatus.LOADING,
            },
        };
    case actionTypes.GET_CASE_COMPLETE:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                caseValue: action.caseValue,
                loadStatus: loadStatus.COMPLETE,
            },
        };
    case actionTypes.GET_CASE_ERROR:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                loadStatus: loadStatus.ERROR,
                error: action.error,
            },
        };
    case actionTypes.SET_CASE_COMPLETE:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                caseValue: action.value,
                loadStatus: loadStatus.COMPLETE,
            },
        };
    case actionTypes.SET_CASE_ERROR:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                caseValue: undefined,
                loadStatus: loadStatus.ERROR,
            },
        };
    case actionTypes.SET_CASE_START:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                caseValue: undefined,
                loadStatus: loadStatus.LOADING,
            },
        };
    case actionTypes.CLEAR_CASE:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                caseValue: undefined,
                loadStatus: undefined,
            },
        };
    case REEVALUATE_SCHEMA: {
        return handleReEvaluateSchemaAction(state, action);
    }
    case dataActionTypes.GET_DATA_COMPLETE: {
        return handleDataComplete(state, action);
    }
    case yangActionsActionTypes.LOAD_ACTION_ACTION: {
        return { ...state,
            [action.path]: {
                ...state[action.path],
                isAction: true,
            },
        };
    }
    default:
        return state;
    }
}

export {
    canMapChangeOp as _canMapChangeOp,
    getRows as _getRows,
    handleChanges as _handleChanges,
    handleDataComplete as _handleDataComplete,
    handleDiffState as _handleDiffState,
    handleModifiedNodes as _handleModifiedNodes,
    handleReEvaluateSchemaAction as _handleReEvaluateSchemaAction,
    handleTransactionChanges as _handleTransactionChanges,
    isActionChild as _isActionChild,
    omitNodes as _omitNodes,
    shouldNodeBeMarkedAsDeleted as _shouldNodeBeMarkedAsDeleted,
    updateDirtyState as _updateDirtyState,
};
