import { formatPath } from '@webui-one/core/utils/path';
import { isObject } from '@webui-one/core/utils/object';
import { isNonEmptyString } from '@webui-one/core/utils/string';
import ApplicationHubRoutes from 'ApplicationHub';
import CommitManagerRoutes from 'CommitManager';
import ConfigurationEditorRoutes from 'ConfigurationEditor';
import DashboardRoutes from 'Dashboard';
import DeviceManagerRoutes from 'DeviceManager';
import ServiceManagerRoutes from 'ServiceManager';

import ApplicationRoot from './ApplicationContainer';
import ErrorPage from './ErrorPage/ErrorPage';

const ErrorRoute = {
    path: '/*',
    component: ErrorPage,
};
const childRoutes = [
    CommitManagerRoutes,
    ConfigurationEditorRoutes,
    DashboardRoutes,
    DeviceManagerRoutes,
    ServiceManagerRoutes,
    ErrorRoute,
];

export default {
    childRoutes: [{
        path: formatPath(),
        component: ApplicationRoot,
        indexRoute: ApplicationHubRoutes,
        childRoutes,
    }],
};

export const isNativeApplication = (app) => {
    let href;
    if (isObject(app) && isNonEmptyString(app.href)) {
        href = app.href;
    } else if (isNonEmptyString(app)) {
        href = app;
    }

    if (!href) {
        return false;
    }

    return childRoutes.reduce((acc, item) => (
        item.path.startsWith(href)
            ? true
            : acc
    ), false);
};
