// NOTE(dbildstr): needed for browsers that do not support Promise (i.e IE11)
import babelPolyfill from '@babel/polyfill'; // eslint-disable-line no-unused-vars
import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import { Router, browserHistory } from 'react-router';
import { syncHistoryWithStore } from 'react-router-redux';
import { exposeDevCode } from 'core/devmode';
import { initializeWindow } from '@webui-one/core/utils/browser';
import { initializeJsonRpc } from '@webui-one/core/jsonrpc';
import CometContainer from 'modules/comet/CometContainer';
import configureStore from './store/configureStore';
import rootRoute from './Routes';

initializeJsonRpc();
initializeWindow();

const store = configureStore();
const history = syncHistoryWithStore(browserHistory, store);

exposeDevCode(store);

ReactDOM.render(
    <Provider store={store}>
        <div>
            <CometContainer
                upgrade
                messages
            />
            <Router history={history} routes={rootRoute} />
        </div>
    </Provider>,
    document.getElementById('root'),
);
