import * as nodesActionTypes from 'modules/nodes/actionTypes';
import * as transActionTypes from 'modules/transactions/actionTypes';
import {
    _handleChanges as handleChanges,
} from 'modules/nodes/reducer';

import * as actionTypes from './actionTypes';

const omitNodes = (state, path) => {
    const matchPath = `${path}/`;
    const newState = Object.keys(state || {})
        .reduce((acc, p) => {
            if (!p.startsWith(matchPath) && p !== path) {
                acc[p] = state[p];
            }
            return acc;
        }, {});

    return newState;
};

export const handleGetDataStart = (state, action) => ({
    ...state,
    config: {
        ...state.config,
        isLoading: true,
        [action.path]: {
            ...state[action.path],
            isLoading: true,
        },
    },
    error: { ...omitNodes(state.error, action.path) },
});

export const handleGetDataComplete = (state, action) => ({
    ...state,
    config: {
        ...omitNodes(state.config, action.path),
        ...{
            ...action.data,
            [action.path]: {
                ...action.data[action.path],
                isLoading: false,
            },
        },
        isLoading: false,
    },
    error: { ...omitNodes(state.error, action.path) },
});

export const handleGetDataError = (state, action) => ({
    ...state,
    config: {
        ...state.config,
        isLoading: false,
        [action.path]: {
            ...state.config[action.path],
            isLoading: false,
        },
    },
    error: {
        ...state.error,
        [action.path]: action.error,
    },
});

export const handleDelete = (state, action) => {
    const config = {
        ...state.config,
    };
    delete config[action.path];
    return {
        ...state,
        config,
    };
};

export const handleDeleteNodeAndChildren = (state, action) => ({
    ...state,
    config: {
        ...omitNodes(state.config, action.path),
    },
});

export const handleTransChanges = (state, action) => {
    const config = action.changes.reduce((acc, change) => {
        if (change.op === 'created') {
            acc[change.path] = {
                ...acc[change.path],
                exists: true,
            };
        }
        if (change.op === 'deleted') {
            delete acc[change.path];
        }

        return acc;
    }, { ...state.config });
    return {
        ...state,
        config,
    };
};

export const handleReloadCaseDataComplete = (state, action) => {
    const { path, data = {} } = action;
    return {
        ...state,
        config: {
            ...omitNodes(state.config, path),
            ...data,
        },
    };
};

const initialState = {
    config: {},
    error: {},
};

const dataReducer = (state = initialState, action) => {
    switch (action.type) {
    case actionTypes.RELOAD_CASE_DATA_COMPLETE: {
        return handleReloadCaseDataComplete(state, action);
    }
    case actionTypes.GET_DATA_COMPLETE: {
        return handleGetDataComplete(state, action);
    }
    case actionTypes.GET_DATA_ERROR: {
        return handleGetDataError(state, action);
    }
    case actionTypes.GET_DATA_START: {
        return handleGetDataStart(state, action);
    }
    case nodesActionTypes.SET_LOCAL_VALUE: {
        return { ...state,
            config: {
                ...state.config,
                [action.path]: {
                    ...state.config[action.path],
                    value: action.value,
                },
            },
        };
    }
    case nodesActionTypes.CREATE:
        return { ...state,
            config: {
                ...state.config,
                [action.path]: {
                    ...state.config[action.path],
                    value: action.value,
                    exists: true,
                    error: undefined,
                    isDeleted: false,
                    default: undefined,
                },
            },
        };
    case nodesActionTypes.DELETE:
        return handleDelete(state, action);
    case nodesActionTypes.DELETE_NODE_AND_CHILDREN:
        return handleDeleteNodeAndChildren(state, action);
    case nodesActionTypes.SET_VALUE_COMPLETE:
        return handleChanges({ ...state,
            config: {
                ...state.config,
                [action.path]: {
                    ...state.config[action.path],
                    newValue: action.value,
                    isLoading: false,
                    error: undefined,
                },
            },
        }, action);
    case nodesActionTypes.GET_VALUE:
        return { ...state,
            config: {
                ...state.config,
                [action.path]: {
                    ...state.config[action.path],
                    value: action.value,
                },
            },
        };
    case nodesActionTypes.QUERY:
        return { ...state,
            config: {
                ...state.config,
                [action.path]: {
                    totalNumberOfResults: action.result.total_number_of_results,
                },
            },
        };
    case transActionTypes.GET_TRANS_CHANGES:
        return handleTransChanges(state, action);
    case transActionTypes.DELETE_TRANS:
        return {
            ...initialState,
        };
    default:
        return state;
    }
};

export default dataReducer;
