import { quoteKey } from '@webui-one/core/functional_utils';

const isNode = Object.prototype.toString.call(typeof process !== 'undefined' ? process : 0) === '[object process]';
const baseUrl = isNode ? 'http://localhost:8080' : '';
const PACKAGE_PATH = '/ncs:packages/package';
export const getPackagePath = name => `${PACKAGE_PATH}{${quoteKey(name)}}`;
export const packageUrl = name => `/custom/${name}`;
export const webuiJsonUrl = name => `${baseUrl}${packageUrl(name)}/webui.json`;
export const packageStatusUrl = name => `${getPackagePath(name)}/oper-status/status`;

