import * as actionTypes from './actionTypes';

const initialState = {
};

export default function yangActions(state = initialState, action) {
    switch (action.type) {
    case actionTypes.RUN_ACTION_START:
        return {
            ...state,
            [action.path]: {
                action: action.path.split('/').pop(),
                resultPending: true,
                startedAt: action.startedAt,
                result: undefined,
                error: undefined,
                status: 'pending',
            },
        };
    case actionTypes.RUN_ACTION_COMPLETE:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                resultPending: false,
                result: action.result,
                error: undefined,
                completedAt: action.completedAt,
                status: 'completed',
            },
        };
    case actionTypes.RUN_CUSTOM_ACTION_COMPLETE:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                resultPending: false,
                result: action.result,
                error: undefined,
                completedAt: action.completedAt,
                status: 'completed',
            },
        };
    case actionTypes.RUN_ACTION_ERROR:
        return {
            ...state,
            [action.path]: {
                ...state[action.path],
                resultPending: false,
                error: action.error,
                result: undefined,
                completedAt: action.completedAt,
                status: 'error',
            },
        };
    case actionTypes.CLEAR_ACTION_OUTPUT:
        return {
            ...state,
            [action.path]: {
                ...state[action.path],
                error: undefined,
                result: undefined,
                completedAt: undefined,
            },
        };
    default:
        return state;
    }
}
