import * as actionTypes from './actionTypes';
import jsonrpcSimple, {
    zipQueryResult,
} from '@webui-one/core/jsonrpc';
import { isNonEmptyArray } from '@webui-one/core/utils/array';
import {
    read,
    write,
} from 'modules/transactions/actions';
import { selectNode } from 'modules/nodes/selectors';
import { deleteThunk } from 'modules/nodes/thunks';

function zipGetValuesResults(leafs, results) {
    if (isNonEmptyArray(leafs) && isNonEmptyArray(results)) {
        return results.map((result, index) => ({
            [leafs[index]]: result.value,
            access: result.access,
        }));
    }
    return undefined;
}

export const clearCaseAction = path => ({
    type: actionTypes.CLEAR_CASE,
    path,
});

export const getCaseCompleteAction = (path, value) => ({
    type: actionTypes.GET_CASE_COMPLETE,
    path,
    caseValue: value,
});

export const getCaseErrorAction = (path, error) => ({
    type: actionTypes.GET_CASE_ERROR,
    path,
    error,
});

export const getCaseStartAction = path => ({
    type: actionTypes.GET_CASE_START,
    path,
});

export const setCaseCompleteAction = (path, value) => ({
    type: actionTypes.SET_CASE_COMPLETE,
    path,
    value,
});

export const setCaseErrorAction = (path, error) => ({
    type: actionTypes.SET_CASE_ERROR,
    path,
    error,
});

export const setCaseStartAction = path => ({
    type: actionTypes.SET_CASE_START,
    path,
});

export const createAction = path => ({ type: actionTypes.CREATE, path });

