export const getValuesPendingAction = (path, leafs) => ({
    type: actionTypes.GET_VALUES_PENDING,
    values: leafs.map(leaf => ({
        path: `${path}${leaf}`,
    })),
});

export const getValuesAction = (path, leafs, results) => ({
    type: actionTypes.GET_VALUES,
    values: leafs.map((leaf, i) => ({
        path: `${path}${leaf}`,
        value: results.values[i].value,
        error: results.values[i].error,
    })),
});

export const getValuesErrorAction = (path, leafs, error) => ({
    type: actionTypes.GET_VALUES_ERROR,
    values: leafs.map(leaf => ({
        path: `${path}${leaf}`,
        error,
    })),
});

export const queryAction = (result, selection, path, sort, sortOrder, chunkSize) => ({
    type: actionTypes.QUERY,
    path,
    sort,
    sortOrder,
    chunkSize,
    result: { ...result, results: zipQueryResult(selection, result.results) },
});

export const queryErrorAction = (error, selection, path) => ({
    type: actionTypes.QUERY_ERROR,
    error,
    path,
    selection,
});

export const queryPendingAction = path => ({
    type: actionTypes.QUERY_PENDING,
    path,
});

