export const modifiedNodesAction = paths => ({
    type: actionTypes.MODIFIED_NODES,
    paths,
});

export const modifiedNodes = paths =>
    dispatch => dispatch(modifiedNodesAction(paths));

export const getLeafrefValues = (
    path,
    {
        funcs: {
            jsonrpc = jsonrpcSimple,
            readThunk = read,
        } = {},
    } = {},
) => (
    (dispatch) => {
        dispatch(getLeafrefValuesStartAction(path));
        return dispatch(readThunk({ path })).then(({ th }) => (
            jsonrpc('get_leafref_values', { th, path, skip_grouping: true }).then(res => (
                dispatch(getLeafrefValuesCompleteAction(path, res.values))
            ))
        ).catch(error => (
            dispatch(getLeafrefValuesErrorAction(path, error))
        )));
    }
);

const getLeafrefValuesForCreate = (
    path,
    createPath,
    {
        funcs: {
            jsonrpc = jsonrpcSimple,
            readThunk = read,
        } = {},
    } = {},
) => dispatch => (
    dispatch(readThunk({ path })).then(({ th }) => (
        jsonrpc('get_leafref_values', { th, path: createPath, skip_grouping: true }).then(res => (
            dispatch(getLeafrefValuesCompleteAction(path, res.values))
        ))
    )).catch(error => (
        dispatch(getLeafrefValuesErrorAction(path, error))
    ))
);

export const query = ({
    selection,
    path,
    sort,
    sortOrder,
    initialOffset,
    chunkSize,
    result_as = 'leaf_value_as_string', // eslint-disable-line camelcase
    xpathExpr,
    contextNode,
    funcs: {
        jsonrpc = jsonrpcSimple,
        readThunk = read,
    } = {},
} = {}) => (dispatch) => {
    dispatch(queryPendingAction(path));
    return dispatch(readThunk({ path })).then(({ th }) => {
        const params = {
            th,
            path,
            selection,
            result_as,
        };
        if (sort !== undefined) params.sort = Array.isArray(sort) ? sort : [sort];
        if (sortOrder !== undefined) params.sort_order = sortOrder;
        if (xpathExpr !== undefined) params.xpath_expr = xpathExpr;
        if (contextNode !== undefined) params.context_node = contextNode;
        if (initialOffset || chunkSize) {
            params.initial_offset = initialOffset || 1;
            params.chunk_size = chunkSize || 10;
        }

        return jsonrpc('query', params).then(result =>
            dispatch(queryAction(
                result,
                selection,
                path,
                sort,
                sortOrder,
                chunkSize,
            )),
        );
    }).catch(error =>
        dispatch(queryErrorAction(error, selection, path)),
    );
};



export const getValues = (
    path,
    leafs,
    {
        funcs: {
            readThunk = read,
            jsonrpc = jsonrpcSimple,
        } = {},
    } = {},
) => (dispatch) => {
    dispatch(getValuesPendingAction(path, leafs));
    return dispatch(readThunk({ path })).then(({ th }) => (
        jsonrpc('get_values', { th, path, leafs }).then(result => (
            dispatch(getValuesAction(path, leafs, result))
        ))
    )).catch((error) => {
        dispatch(getValuesErrorAction(path, leafs, error));
    });
};

function getValue(
    path,
    {
        funcs: {
            jsonrpc = jsonrpcSimple,
            readThunk = read,
        } = {},
    } = {},
) {
    return (dispatch) => {
        dispatch(getValuePendingAction(path));
        return dispatch(readThunk({ path })).then(({ th }) => (
            jsonrpc('get_value', { th, path }).then(result => (
                dispatch(getValueAction(path, result.value))
            ))
        )).catch((error) => {
            if (error.type === 'data.not_found') {
                dispatch(getValueAction(path, undefined));
            } else {
                dispatch(getValueErrorAction(path, error));
            }
        });
    };
}

function getValueCached(
    path,
    isConfigFalseCallpoint,
    {
        funcs: {
            getValueThunk = getValue,
        } = {},
    } = {},
) {
    return (dispatch, getState) => {
        const node = selectNode(getState(), path);
        if (!isConfigFalseCallpoint && Object.keys(node).indexOf('value') > -1 && !node.isDeleted) {
            return Promise.resolve(dispatch(getValueAction(path, node.value)));
        }

        return dispatch(getValueThunk(path));
    };
}

function setValue(
    path,
    value,
    {
        funcs: {
            jsonrpc = jsonrpcSimple,
            writeThunk = write,
        } = {},
    } = {},
) {
    return (dispatch) => {
        dispatch(setValueStartAction(path, value));
        return dispatch(writeThunk({ path })).then(({ th }) =>
            jsonrpc('set_value', { th, path, value }).then(() =>
                dispatch(setValueAction(path, value)),
            ).catch((error) => {
                if(error && error.data && error.data.reason === '"" is not a valid value.') {
                    dispatch(deleteThunk({ caseAncestors: null, dependants: null, path }));
                    dispatch(setValueInterruptAction(path));
                } else {
                    dispatch(setValueErrorAction(path, value, error));
                }
            },
            ),
        );
    };
}

export {
    getLeafrefValuesForCreate,

    getValue,
    getValueCached,
    setValue,

    zipGetValuesResults,
};
