import { omitKeys } from '@webui-one/core/utils/object';
import {
    DBS,
    MODES,
} from '@webui-one/core/jsonrpc/constants';

import * as actionTypes from './actionTypes';
import { transactionStatus } from './constants';
import * as yangActionTypes from '../yangActions/actionTypes';


const initialState = {
    commitOptions: {},
    db: DBS.RUNNING,
    read: undefined,
    read_write: undefined,
    pendingRead: undefined,
    writePending: undefined,
};


const determineTransactionStatus = (action) => {
    if (action.error) {
        return transactionStatus.INVALID;
    }
    if (action.warnings) {
        return transactionStatus.WARNING;
    }
    return transactionStatus.VALID;
};

const handleGetTrans = (state, action) => {
    const trans = action.transactions.reduce((acc, cur) => {
        const { th } = cur;
        acc[th] = cur;
        return acc;
    }, {});
    return {
        ...state,
        trans,
    };
};

const findInState = ({ trans = {} }, { actionPath, db, mode, tag } = {}) => (
    Object.keys(trans).reduce((acc, key) => {
        const val = trans[key];
        if (val.actionPath === actionPath
            && val.db === db
            && val.mode === mode
            && val.tag === tag) {
            return val;
        }
        return acc;
    }, undefined)
);

const handleRead = (state, action) => {
    const { th, mode, db, tag } = action;

    const transInState = findInState(state, action);
    if (transInState !== undefined) {
        // We should not add two read transactions with same db and tag
        return {
            ...state,
            pendingRead: false,
        };
    }

    return {
        ...state,
        pendingRead: false,
        trans: {
            ...state.trans,
            [th]: {
                db,
                mode,
                tag,
                th,
            },
        },
    };
};

const handleWrite = (state, action) => {
    const { actionPath, th, mode, db, tag } = action;

    const transInState = findInState(state, action);
    if (transInState !== undefined) {
        // We should not add two write transactions with same db and tag
        return {
            ...state,
            writePending: false,
        };
    }

    const thValue = {
        db,
        mode,
        tag,
        th,
    };

    if (actionPath) thValue.actionPath = actionPath;

    return {
        ...state,
        // read_write: action.read_write,
        writePending: false,
        trans: {
            ...state.trans,
            [th]: thValue,
        },
    };
};

const handleCommitDryRunComplete = (state, action) => {
    const { th, result } = action;
    return {
        ...state,
        trans: {
            ...state.trans,
            [th]: {
                ...state.trans[th],
                commitDryRunResult: result,
                commitDryRunInProgress: false,
                hasLatestCommitDryRunResult: true,
            },
        },
    };
};

const handleCommitDryRunError = (state, action) => {
    const { th, error } = action;
    return {
        ...state,
        trans: {
            ...state.trans,
            [th]: {
                ...state.trans[th],
                commitDryRunError: error,
                commitDryRunInProgress: false,
            },
        },
    };
};

const handleCommitDryRunStart = (state, action) => {
    const { th } = action;
    return {
        ...state,
        trans: {
            ...state.trans,
            [th]: {
                ...state.trans[th],
                commitDryRunError: undefined,
                commitDryRunInProgress: true,
                commitDryRunResult: undefined,
            },
        },
    };
};

const handleCommitComplete = (state, action) => {
    const { th } = action;

    return {
        ...state,
        trans: {
            ...omitKeys(state.trans, [`${th}`]),
        },
    };
};

const handleCommitError = (state, action) => {
    const { th, error } = action;
    return {
        ...state,
        trans: {
            ...state.trans,
            [th]: {
                ...state.trans[th],
                commitInProgress: false,
                error,
            },
        },
    };
};

const handleCommitStart = (state, action) => {
    const { th } = action;
    return {
        ...state,
        trans: {
            ...state.trans,
            [th]: {
                ...state.trans[th],
                commitInProgress: true,
            },
        },
    };
};

const handleDeleteTrans = (state, action) => {
    const { th } = action;
    return {
        ...state,
        trans: {
            ...omitKeys(state.trans, [`${th}`]),
        },
    };
};

const handleRunActionComplete = (state, action) => {
    const { newTh, oldTh, path: actionPath } = action;
    return {
        ...state,
        trans: {
            ...omitKeys(state.trans, [`${oldTh}`]),
            [newTh]: {
                th: newTh,
                actionPath,
                db: DBS.CS_TRANS,
                mode: MODES.READ_WRITE,
            },
        },
    };
};

const handleGetTransChanges = (state, action) => {
    const { th, changes } = action;
    return {
        ...state,
        trans: {
            ...state.trans,
            [th]: {
                ...state.trans[th],
                changes,
                hasLatestCommitDryRunResult: false,
            },
        },
    };
};

const handleGetWriteTrans = (state, action) => {
    const { th, mode, db, tag } = action;

    const cleanedState = Object.keys(state.trans).reduce((acc, key) => {
        const val = state.trans[key];

        if (val.th !== th && val.mode === mode && val.db === db && val.tag === tag) {
            return acc;
        }
        acc[key] = val;
        return acc;
    }, {});

    return {
        ...state,
        trans: {
            ...cleanedState,
            [th]: {
                ...state.trans[th],
                th,
                mode,
                db,
                tag,
            },
        },
    };
};

const handleNativeCommitDryRunComplete = (state, action) => {
    const { th, result } = action;
    return {
        ...state,
        trans: {
            ...state.trans,
            [th]: {
                ...state.trans[th],
                nativeCommitDryRunResult: result,
                nativeCommitDryRunInProgress: false,
            },
        },
    };
};

const handleNativeCommitDryRunError = (state, action) => {
    const { th, error } = action;
    return {
        ...state,
        trans: {
            ...state.trans,
            [th]: {
                ...state.trans[th],
                nativeCommitDryRunError: error,
                nativeCommitDryRunInProgress: false,
            },
        },
    };
};

const handleNativeCommitDryRunStart = (state, action) => {
    const { th } = action;
    return {
        ...state,
        trans: {
            ...state.trans,
            [th]: {
                ...state.trans[th],
                nativeCommitDryRunError: undefined,
                nativeCommitDryRunInProgress: true,
                nativeCommitDryRunResult: undefined,
            },
        },
    };
};

const handleReEvaluateComplete = (state, action) => {
    const { th } = action;

    return {
        ...state,
        trans: {
            ...state.trans,
            [th]: {
                ...state.trans[th],
                thIsBusy: false,
            },
        },
    };
};

const handleReEvaluateError = (state, action) => {
    const { th } = action;

    return {
        ...state,
        trans: {
            ...state.trans,
            [th]: {
                ...state.trans[th],
                thIsBusy: false,
            },
        },
    };
};

const handleReEvaluateStart = (state, action) => {
    const { th } = action;
    return {
        ...state,
        trans: {
            ...state.trans,
            [th]: {
                th,
                mode: state.trans[th].mode,
                db: state.trans[th].db,
                tag: state.trans[th].tag,
                thIsBusy: true,
            },
        },
    };
};

const handleValidateTrans = (state, action) => {
    const { th, warnings } = action;
    return {
        ...state,
        trans: {
            ...state.trans,
            [th]: {
                ...state.trans[th],
                validateTransError: undefined,
                warnings,
                status: determineTransactionStatus(action),
            },
        },
    };
};

const handleValidateTransError = (state, action) => {
    const { th, error } = action;
    return {
        ...state,
        trans: {
            ...state.trans,
            [th]: {
                ...state.trans[th],
                status: determineTransactionStatus(action),
                validateTransError: error,
            },
        },
    };
};

const handleValidateCommitError = (state, action) => {
    const { th, error } = action;
    return {
        ...state,
        trans: {
            ...state.trans,
            [th]: {
                ...state.trans[th],
                error,
            },
        },
    };
};

const handleTransConflicts = (state, action) => {
    const { th, conflicts } = action;
    return {
        ...state,
        trans: {
            ...state.trans,
            [th]: {
                ...state.trans[th],
                conflicts,
            },
        },
    };
};

export default function transactions(state = initialState, action) {
    switch (action.type) {
    case actionTypes.COMMIT_DRY_RUN_COMPLETE:
        return handleCommitDryRunComplete(state, action);
    case actionTypes.COMMIT_DRY_RUN_ERROR:
        return handleCommitDryRunError(state, action);
    case actionTypes.COMMIT_DRY_RUN_START:
        return handleCommitDryRunStart(state, action);
    case actionTypes.COMMIT_ERROR:
        return handleCommitError(state, action);
    case actionTypes.COMMIT_START:
        return handleCommitStart(state, action);
    case actionTypes.COMMIT_COMPLETE:
        return handleCommitComplete(state, action);
    case actionTypes.DELETE_TRANS:
        return handleDeleteTrans(state, action);
    case actionTypes.GET_TRANS_CHANGES:
        return handleGetTransChanges(state, action);
    case actionTypes.GET_WRITE_TRANS:
        return handleGetWriteTrans(state, action);
    case actionTypes.GET_TRANS:
        return handleGetTrans(state, action);
    case actionTypes.NATIVE_COMMIT_DRY_RUN_START:
        return handleNativeCommitDryRunStart(state, action);
    case actionTypes.NATIVE_COMMIT_DRY_RUN_COMPLETE:
        return handleNativeCommitDryRunComplete(state, action);
    case actionTypes.NATIVE_COMMIT_DRY_RUN_ERROR:
        return handleNativeCommitDryRunError(state, action);
    case actionTypes.READ:
        return handleRead(state, action);
    case actionTypes.READ_PENDING:
        return { ...state, pendingRead: action.pendingRead };
    case actionTypes.REEVALUATE_START:
        return handleReEvaluateStart(state, action);
    case actionTypes.REEVALUATE_ERROR:
        return handleReEvaluateError(state, action);
    case actionTypes.REEVALUATE_COMPLETE:
        return handleReEvaluateComplete(state, action);
    case actionTypes.START_RESOLVE:
        return {
            ...state,
            trans: {
                ...state.trans,
                [action.th]: {
                    ...state.trans[action.th],
                    status: undefined,
                    thIsBusy: true,
                },
            },
        };
    case actionTypes.COMPLETE_RESOLVE:
        return {
            ...state,
            trans: {
                ...state.trans,
                [action.th]: {
                    ...state.trans[action.th],
                    status: transactionStatus.VALID,
                    thIsBusy: false,
                    conflicts: [],
                },
            },
        };
    case actionTypes.ERROR_RESOLVE:
        return {
            ...state,
            trans: {
                ...state.trans,
                [action.th]: {
                    ...state.trans[action.th],
                    status: determineTransactionStatus(action),
                    error: action.error,
                    thIsBusy: false,
                },
            },
        };
    case actionTypes.CONFLICTS:
        return handleTransConflicts(state, action);
    case actionTypes.WRITE:
        return handleWrite(state, action);
    case actionTypes.WRITE_PENDING:
        return { ...state, writePending: action.writePending };
    case actionTypes.VALIDATE_COMMIT_ERROR:
        return handleValidateCommitError(state, action);
    case actionTypes.VALIDATE_TRANS_ERROR:
        return handleValidateTransError(state, action);
    case actionTypes.VALIDATE_TRANS:
        return handleValidateTrans(state, action);
    case actionTypes.SET_COMMIT_OPTIONS:
        return {
            ...state,
            commitOptions: {
                ...state.commitOptions,
                [action.option]: action.isEnabled,
            },
        };
    case actionTypes.GET_ROLLBACKS:
        return {
            ...state,
            rollbacks: action.rollbacks,
        };
    case actionTypes.ROLLBACK_START:
        return {
            ...state,
            rollbackLoadError: null,
            loadInProgress: true,
        };
    case actionTypes.ROLLBACK_LOADED:
        return {
            ...state,
            rollbackLoadError: null,
            loadInProgress: false,
        };
    case actionTypes.ROLLBACK_LOAD_ERROR:
        return {
            ...state,
            rollbackLoadError: action.error,
            loadInProgress: false,
        };
    case actionTypes.ROLLBACK_READ: {
        const rollbacks = [...state.rollbacks];
        rollbacks[action.nr] = { ...rollbacks[action.nr], rollback: action.rollback };
        return {
            ...state,
            rollbacks,
        };
    }
    case actionTypes.UPGRADE_COMMIT:
        return {
            ...state,
            upgradePending: true,
            pendingRead: false,
            trans: {},
        };
    case actionTypes.UPGRADE_COMPLETE:
        return {
            ...state,
            upgradePending: false,
            pendingRead: false,
            trans: {},
        };
    case actionTypes.UPGRADE_INIT:
        return {
            ...state,
            upgradePending: true,
            trans: {},
        };
    case actionTypes.LOAD_START:
        return {
            ...state,
            loadInProgress: true,
        };
    case actionTypes.LOAD_FROM_TEXT_SUCCESS:
        return {
            ...state,
            loadFromTextError: null,
            loadInProgress: false,
        };
    case actionTypes.LOAD_FROM_TEXT_ERROR:
        return {
            ...state,
            loadFromTextError: action.error,
            loadInProgress: false,
        };
    case actionTypes.LOAD_FROM_FILE_SUCCESS:
        return {
            ...state,
            loadFromFileError: null,
            loadInProgress: false,
        };
    case actionTypes.LOAD_FROM_FILE_ERROR:
        return {
            ...state,
            loadFromFileError: action.error,
            loadInProgress: false,
        };
    case yangActionTypes.RUN_ACTION_COMPLETE:
        return handleRunActionComplete(state, action);
    default:
        return state;
    }
}

export {
    handleCommitComplete as _handleCommitComplete,
    handleCommitDryRunComplete as _handleCommitDryRunComplete,
    handleCommitDryRunError as _handleCommitDryRunError,
    handleCommitDryRunStart as _handleCommitDryRunStart,
    handleCommitError as _handleCommitError,
    handleCommitStart as _handleCommitStart,
    handleDeleteTrans as _handleDeleteTrans,
    handleGetTrans as _handleGetTrans,
    handleGetTransChanges as _handleGetTransChanges,
    handleGetWriteTrans as _handleGetWriteTrans,
    handleNativeCommitDryRunComplete as _handleNativeCommitDryRunComplete,
    handleNativeCommitDryRunError as _handleNativeCommitDryRunError,
    handleNativeCommitDryRunStart as _handleNativeCommitDryRunStart,
    handleRead as _handleRead,
    handleReEvaluateComplete as _handleReEvaluateComplete,
    handleReEvaluateError as _handleReEvaluateError,
    handleReEvaluateStart as _handleReEvaluateStart,
    handleRunActionComplete as _handleRunActionComplete,
    handleValidateCommitError as _handleValidateCommitError,
    handleValidateTrans as _handleValidateTrans,
    handleValidateTransError as _handleValidateTransError,
    handleWrite as _handleWrite,
    determineTransactionStatus as _determineTransactionStatus,
};
