import {
    getSchema as _getSchema,
    getTrans as _getTrans,
    jsonrpcBatch as _jsonrpcBatch,
} from '@webui-one/core/jsonrpc';
import { pathToObjects } from '@webui-one/core/keypath/kp-parser';
import { last, zipObject } from '@webui-one/core/utils/array';
import { assignDeep, pathOr } from '@webui-one/core/utils/object';
import { read as _read } from 'modules/transactions/actions';
import {
    selectIsLoaded as _selectIsLoaded,
    selectSchema,
} from './selectors';
import {
    parseGetSchemaResult as _parseGetSchemaResult,
    parseGetSchemaError as _parseGetSchemaError,
} from './schemas';
import * as actionTypes from './actionTypes';

export const getSchemasStartAction = path => ({
    type: actionTypes.GET_SCHEMAS_START,
    path,
});

export const getSchemasCompleteAction = (path, schemas, types) => ({
    type: actionTypes.GET_SCHEMAS_COMPLETE,
    path,
    schemas,
    types,
});

export const getSchemasErrorAction = (path, errors) => ({
    type: actionTypes.GET_SCHEMAS_ERROR,
    path,
    errors,
});

export const reEvaluateSchemaAction = (path, dependants) => ({
    type: actionTypes.REEVALUATE_SCHEMA,
    path,
    dependants,
});

const createRequestMapper = th => path => ({
    method: 'get_schema',
    params: {
        th,
        levels: 2,
        path,
        stop_on_list: true,
        evaluate_when_entries: true,
    },
});

const getSchemasFromResponses = (
    responses,
    {
        funcs: {
            parseGetSchemaResult = _parseGetSchemaResult,
            parseGetSchemaError = _parseGetSchemaError,
        } = {},
    } = {},
) => (
    Object.keys(responses).reduce((acc, path) => {
        const { error, result } = responses[path];

        if (error) {
            assignDeep(acc, parseGetSchemaError(path, error));
        } else {
            assignDeep(acc, parseGetSchemaResult(path, result));
        }
        return acc;
    }, {})
);

const getTypesFromResponses = responses => (
    Object.keys(responses).reduce((acc, path) => {
        const { error, result } = responses[path];
        if (!error) {
            const parsedTypes = result.meta.types;
            assignDeep(acc, parsedTypes);
        }
        return acc;
    }, {})
);

const getNotLoadedPaths = ({
    path,
    reload,
    single,
    getState,
    funcs: {
        selectIsLoaded = _selectIsLoaded,
    } = {},
} = {}) => {
    if (single) return [path];

    const paths = pathToObjects(path)
        .filter(po => po.list !== true)
        .reduce((acc, cur) => {
            const isLoaded = selectIsLoaded(getState(), { path: cur.path });
            if (reload || isLoaded === false) acc.push(cur.path);
            return acc;
        }, []);
    return paths;
};

export const getSchema = ({
    path,
    reload,
    single,
    funcs: {
        read = _read,
        jsonrpcBatch = _jsonrpcBatch,
        getPaths = getNotLoadedPaths,
    } = {},
} = {}) => async (dispatch, getState) => {
    const paths = getPaths({ path, single, reload, getState });
    if (paths.length === 0) return undefined;

    try {
        dispatch(getSchemasStartAction(path));

        const { th } = await read()(dispatch, getState);

        const requests = paths.map(createRequestMapper(th));

        const results = await jsonrpcBatch({ requests });
        const responses = zipObject(paths, results);

        const types = getTypesFromResponses(results);
        const schemas2 = getSchemasFromResponses(responses);

        dispatch(getSchemasCompleteAction(path, schemas2, types));

        const schema = selectSchema({ schemas2, types }, { path });
        // NOTE: backward compatibility
        // Some calling functions needs a dispatched action (doesn't care about the type)
        return dispatch({ type: actionTypes.NOOP, schema });
    } catch (errors) {
        dispatch(getSchemasErrorAction(path, errors));
    }
    return undefined;
};

const getParentPath = (parent) => {
    if (parent === undefined) return undefined;
    return parent.kindpath.reduce((acc, cur) => {
        if (cur.kind === 'choice') return acc;
        if (cur.kind === 'case') return acc;
        return `${acc}/${cur.name}`;
    }, '');
};

export const getSchemaWithKindpath = async ({
    path,
    funcs: {
        getTrans = _getTrans,
        getSchemaFunc = _getSchema,
    } = {},
} = {}) => {
    const th = await getTrans();
    const parsed = pathToObjects(path);
    const responses = await parsed.reduce(async (previousPromise, cur) => {
        const acc = await previousPromise;
        if (cur.list === true) return acc;

        const { name, relativeName } = cur;

        const parent = last(acc);
        const parentKindpath = pathOr([], ['kindpath'], parent);

        if (parent && parent.data.children) {
            const child = parent.data.children.find(c => c.name === name);
            if (child && child.kind === 'choice') {
                parent.kindpath = [
                    ...parentKindpath,
                    { kind: 'choice', name: cur.relativeName },
                ];
                return acc;
            }

            const { kind: parentKind, name: parentName } = last(parent.kindpath);

            if (parentKind === 'choice') {
                const choice = parent.data.children.find(c => c.name === parentName);
                const cse = choice.cases.find(c => c.name === name);
                if (cse) {
                    parent.kindpath = [
                        ...parentKindpath,
                        { kind: 'case', name: cur.relativeName },
                    ];
                }
                return acc;
            }
        }

        const parentPath = getParentPath(parent);
        const curPath = parentPath ? `${parentPath}/${relativeName}` : `/${relativeName}`;
        const response = await getSchemaFunc({ th, path: curPath, levels: 1 });

        response.kindpath = [
            ...parentKindpath,
            { kind: response.data.kind, name: cur.relativeName },
        ];

        acc.push(response);
        return acc;
    }, Promise.resolve([]));
    return last(responses);
};

export {
    getNotLoadedPaths as _getNotLoadedPaths,
    getSchemasFromResponses,
    getTypesFromResponses,
};
