import {
    getValue,
    setValue,
} from '@webui-one/core/jsonrpc';

import { read } from 'modules/transactions/actions';
import { METADATA_TAG, userdataPath } from 'modules/userdata/paths';
import {
    directCommit,
} from 'modules/transactions/directCommit';
import * as userdataActionTypes from './actionTypes';

export const fetchUserdataPendingAction = () => ({
    type: userdataActionTypes.FETCH_USERDATA_PENDING,
});

export const fetchUserdataEndAction = () => ({
    type: userdataActionTypes.FETCH_USERDATA_END,
});

export const setUserdataAction = (data, path) => ({
    type: userdataActionTypes.SET_USERDATA,
    data,
    path,
});

const updateUserdataInternal = (name, key, value) => (
    th => (
        setValue({ th, path: userdataPath(name, key), value })
    )
);

export const updateUserdata = (name, key, value, cb) => (
    (dispatch) => {
        const callback = typeof cb === 'function' ? cb : updateUserdataInternal;
        const tag = 'webui-one-update-user-data';
        return dispatch(
            directCommit(
                'UPDATE_USER_DATA',
                tag,
                callback(name, key, value),
            ),
        );
    }
);

export const updateUserMetadata = (name, value, cb) => (
    updateUserdata(name, METADATA_TAG, value, cb)
);

export const parseUserdata = value => (
    JSON.parse(typeof value === 'string' ? value.replace(/\n/g, '\\n') : '{}')
);

export const fetchStateFromCdb = (user, key) => (
    (dispatch) => {
        dispatch(fetchUserdataPendingAction());
        const path = userdataPath(user, key);
        return dispatch(read()).then(({ th }) => (
            getValue({ th, path }).then((result) => {
                dispatch(fetchUserdataEndAction());
                return dispatch(setUserdataAction(
                    parseUserdata(result && result.value),
                    path,
                ));
            }, () => {
                dispatch(fetchUserdataEndAction());
                return dispatch(setUserdataAction(
                    Object.freeze({}),
                    path,
                ));
            })
        ));
    }
);


export {
    parseUserdata as _parseUserdata,
    updateUserdataInternal as _updateUserdataInternal,
};
