import React from 'react';
import PropTypes from 'prop-types';
import ModalConfirm from '@webui-one/components/Modal/ModalConfirm';
import { isFunction } from '@webui-one/core/utils/functional';
import style from './ConfirmStyles';
import { ConfirmStatus } from './actions';

export const hasPendingMessage = confirm => (
    confirm && typeof confirm.message === 'string' && isFunction(confirm.callback)
);

export default class Confirm extends React.Component {
    constructor(props) {
        super(props);
        this.closeModal = this.closeModal.bind(this);
        this.onConfirm = this.onConfirm.bind(this);
        this.onCancel = this.onCancel.bind(this);
        this.state = {
            shouldShowModal: hasPendingMessage(props.confirm),
        };
    }

    // eslint-disable-next-line camelcase, react/sort-comp
    UNSAFE_componentWillReceiveProps(nextProps) {
        const { confirm } = nextProps;

        const hasMessage = hasPendingMessage(confirm);
        if (hasMessage !== this.state.shouldShowModal) {
            this.setState({ shouldShowModal: hasMessage });
        }
    }

    onConfirm() {
        const { callback } = this.props.confirm;
        callback(ConfirmStatus.CONFIRM);
        this.closeModal();
    }

    onCancel() {
        const { callback } = this.props.confirm;
        callback(ConfirmStatus.CANCEL);
        this.closeModal();
    }

    closeModal() {
        this.setState({ shouldShowModal: false });
        this.props.hideConfirm();
    }

    render() {
        const { confirm } = this.props;
        const { shouldShowModal } = this.state;
        return (
            <span data-handle="confirm-handler" style={style.placeHolder}>
                <ModalConfirm
                    title={confirm.title}
                    formIsValid
                    onCancel={this.onCancel}
                    onConfirm={this.onConfirm}
                    shouldRender={shouldShowModal}
                    onRequestClose={this.closeModal}
                >
                    <div style={style.content}>
                        <div data-handle="confirm-message" style={style.confirmMessage}>
                            {confirm.message}
                        </div>
                    </div>
                </ModalConfirm>
            </span>);
    }
}

Confirm.propTypes = {
    hideConfirm: PropTypes.func,
    confirm: PropTypes.shape(Object),
};
