import * as actionTypes from './actionTypes';

const updatePackages = (state, pkgs) => {
    const prefixes = Object.keys(pkgs);
    return prefixes.reduce((acc, prefix) => {
        const pkg = pkgs[prefix];
        const version = pkg['package-version'] || 'N/A';
        acc[prefix] = {
            ...acc[prefix],
            description: pkg.description,
            name: pkg.name,
            status: pkg.status,
            version,
        };
        return acc;
    }, {});
};

const initialState = {};

export default function packages(state = initialState, action) {
    switch (action.type) {
    case actionTypes.PACKAGE_WEBUI:
        return {
            ...state,
            [action.package]: {
                ...state[action.package],
                webui: true,
                data: action.data || {},
            },
        };
    case actionTypes.PACKAGE_STATUS:
        return {
            ...state,
            [action.package]: {
                ...state[action.package],
                status: action.status,
            },
        };
    case actionTypes.PACKAGES_GET:
        return updatePackages(state, action.packages);
    default:
        return state;
    }
}

export const selectPackages = state => state.packages;
