import { formatPath } from '@webui-one/core/utils/path';

export const DASHBOARD_NAME = 'Dashboard';
export default {
    path: formatPath(DASHBOARD_NAME),
    getComponent(nextState, cb) {
        import(/* webpackChunkName: "Dashboard" */'./Dashboard').then((Dashboard) => {
            cb(null, Dashboard.default);
        });
    },
};
