import React from 'react';
import PropTypes from 'prop-types';
import Modal from '@webui-one/components/Modal/Modal';
import { Messages, translate } from 'modules/intl';
import style from './ErrorHandlerStyles';

export const EmptyContent = () => (<div>
    {translate(Messages.ERRORHANDLER.NO_ERRORS_LABEL_TEXT)}
</div>);

export default class ErrorHandler extends React.Component {
    constructor(props) {
        super(props);
        this.handleChange = this.handleChange.bind(this);
        this.closeModal = this.closeModal.bind(this);
        this.goNext = this.goNext.bind(this);
        this.goPrevious = this.goPrevious.bind(this);
        this.go = this.go.bind(this);
        this.state = {
            shouldShowModal: false,
            shouldIgnoreFurtherErrors: false,
        };
    }

    // eslint-disable-next-line camelcase, react/sort-comp
    UNSAFE_componentWillReceiveProps(nextProps) {
        const {
            errorKeys,
            hasNewErrors,
            onReadError,
            shouldIngoreErrors,
        } = nextProps;
        const { shouldShowModal } = this.state;

        if (!shouldIngoreErrors &&
            !shouldShowModal &&
            hasNewErrors) {
            this.setState({ shouldShowModal: true });
            onReadError(errorKeys.slice(-1)[0]);
        }
    }

    getErrorContent() {
        const { error: err } = this.props;
        const { error, date, reason } = err;
        const dateFormatted = (new Date(date)).toLocaleString();
        return (
            <div style={style.errorData}>
                <div style={style.errorMessage}>{` ${error}`}</div>
                <div style={style.errorMessage}>{reason}</div>
                <div style={style.date}>{dateFormatted}</div>
            </div>
        );
    }


    getErrorControls() {
        const { errorCount, errorKeys, selected } = this.props;
        const canGo = errorCount > 1;
        const selectedIndex = errorKeys.findIndex(el => el === selected);
        return (<div id="errorControllers" style={style.controllers}>
            <div style={style.navigationGroup}>
                <button
                    style={style.arrowButton}
                    disabled={!canGo}
                    onClick={this.goPrevious}
                >
                    
                </button>
                <span style={style.stats}>
                    { selectedIndex + 1 } / { errorCount }
                </span>
                <button
                    style={style.arrowButton}
                    disabled={!canGo}
                    onClick={this.goNext}
                >
                    
                </button>
                <div style={style.ignoreLabel}>
                    <input
                        id="shouldIgnoreFurtherErrors"
                        type="checkbox"
                        style={style.checkbox}
                        checked={this.state.shouldIgnoreFurtherErrors}
                        onChange={this.handleChange}
                    />
                    <label htmlFor="shouldIgnoreFurtherErrors" style={style.ignoreLabelText}>
                        {translate(Messages.ERRORHANDLER.IGNORE_LABEL_TEXT)}
                    </label>
                </div>
            </div>
            <div style={style.navigationGroup}>
                <button
                    style={style.button}
                    onClick={this.closeModal}
                >{translate(Messages.ERRORHANDLER.CLOSE_BUTTON_TITLE)}</button>
            </div>
        </div>
        );
    }

    handleChange() {
        this.setState(state => ({
            shouldIgnoreFurtherErrors: !state.shouldIgnoreFurtherErrors,
        }));
    }

    closeModal() {
        const { onPruneErrors, onSetIngoreErrors } = this.props;
        const { shouldIgnoreFurtherErrors } = this.state;
        this.setState({ shouldShowModal: false });
        onSetIngoreErrors(shouldIgnoreFurtherErrors);
        onPruneErrors();
    }

    goPrevious() {
        this.go(-1);
    }

    goNext() {
        this.go(1);
    }

    go(diff) {
        const { errorCount, errorKeys, onReadError, selected } = this.props;
        const currentIndex = errorKeys.findIndex(key => selected === key);
        const nextIndex = (diff + currentIndex + errorCount) % errorCount;
        const errorId = errorKeys[nextIndex];
        onReadError(errorId);
    }

    render() {
        const { errorCount, shouldIngoreErrors, error } = this.props;
        if (shouldIngoreErrors) return false;
        const hasContent = errorCount > 0 && error !== undefined;
        const content = hasContent ? this.getErrorContent() : <EmptyContent />;
        const controls = this.getErrorControls();
        return (
            <span style={style.placeHolder}>
                <Modal
                    title={translate(Messages.ERRORHANDLER.ERROR_REPORT)}
                    shouldRender={this.state.shouldShowModal}
                    onRequestClose={this.closeModal}
                >
                    <div style={style.content}>
                        <div data-handle="error-handler" style={style.errorHandler}>
                            {content}
                            {controls}
                        </div>
                    </div>
                </Modal>
            </span>);
    }
}

ErrorHandler.defaultProps = {
    errorKeys: [],
};

ErrorHandler.propTypes = {
    error: PropTypes.shape(Object),
    errorCount: PropTypes.number,
    // TODO(dbildstr): enable this rule when ENG-19360 is fixed
    // eslint-disable-next-line react/no-unused-prop-types
    hasNewErrors: PropTypes.bool,
    errorKeys: PropTypes.arrayOf(PropTypes.number),
    onPruneErrors: PropTypes.func,
    onReadError: PropTypes.func,
    onSetIngoreErrors: PropTypes.func,
    selected: PropTypes.number,
    shouldIngoreErrors: PropTypes.bool,
};
