import breadcrumbs from 'modules/breadcrumbs/reducer';
import comet from 'modules/comet/reducer';
import confirm from 'Common/Confirm/reducer';
import data from 'modules/data/reducer';
import devices from 'modules/devices/reducer';
import error from 'modules/error/reducer';
import flags from 'modules/flags/reducer';
import globalSettings from 'modules/globalSettings/reducer';
import loadingOverlay from 'Common/LoadingOverlay/reducer';
