import React from 'react';
import PropTypes from 'prop-types';
import Loader from '@webui-one/components/Loading/Loader';
import { color11 as color } from '@webui-one/styles/ApplicationDefaultStyles';
import style from './LoadingOverlayStyles';

class LoadingOverlay extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            waiting: false,
        };
    }

    componentDidMount() {
        const { show, waitTime } = this.props;
        if (show) {
            this.setState({ waiting: true });
            this.timer = setTimeout(() => { this.setState({ waiting: false }); }, waitTime);
        }
    }

    // eslint-disable-next-line camelcase, react/sort-comp
    UNSAFE_componentWillReceiveProps(nextProps) {
        const { show, waitTime } = this.props;

        if (show === false && nextProps.show === true) {
            this.setState({ waiting: true });
            this.timer = setTimeout(() => { this.setState({ waiting: false }); }, waitTime);
        }

        if (show === true && nextProps.show === false) {
            this.setState({ waiting: true });
            if (this.timer) clearTimeout(this.timeout);
        }
    }

    shouldComponentUpdate(nextProps, nextState) {
        if (this.props.show !== nextProps.show) return true;
        if (this.state.waiting !== nextState.waiting) return true;
        return false;
    }

    componentWillUnmount() {
        if (this.timer) clearTimeout(this.timer);
    }

    render() {
        const { show } = this.props;
        const { waiting } = this.state;

        if (!show) return null;
        /* So tests can find loader even when waiting */
        if (waiting) return <div data-handle="loading-overlay" />;

        return (
            <div data-handle="loading-overlay" style={style.overlayShow}>
                <div style={style.loadingStyle}>
                    <Loader color={color} />
                </div>
            </div>
        );
    }
}

LoadingOverlay.propTypes = {
    show: PropTypes.bool.isRequired,
    waitTime: PropTypes.number,
};

LoadingOverlay.defaultProps = {
    waitTime: 250,
};

export default LoadingOverlay;
