import { connect } from 'react-redux';

import { hideLoadingOverlayAction } from './actions';
import LoadingOverlay from './LoadingOverlay';
import { selectShow } from './reducer';


const mapStateToProps = state => ({
    show: selectShow(state),
});

const mapDispatchToProps = dispatch => ({
    hideLoading() {
        return dispatch(hideLoadingOverlayAction());
    },
});

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(LoadingOverlay);

export {
    mapDispatchToProps as _mapDispatchToProps,
    mapStateToProps as _mapStateToProps,
};
