import PropTypes from 'prop-types';
import React from 'react';
import { connect } from 'react-redux';
import { getApplicationName } from 'modules/application/utils';
import { selectFlags } from 'modules/flags/selectors';
import { getSystemSetting } from 'modules/systemSetting/actions';
import { isDevEnv } from '@webui-one/core/utils/functional';
import { getLinkWithPath as configurationEditorGetLinkWithPath } from 'ConfigurationEditor';
import { selectPathname } from 'modules/routing/selectors';
import Application from './Application';


const setDocumentTitle = (applicationName, path) => {
    let title = '';
    if (applicationName) {
        title = `${applicationName}`;
    }
    if (path && applicationName && applicationName === 'Configuration editor') {
        const configPathWithoutRoot = path.replace(configurationEditorGetLinkWithPath(''), '');
        const shortPath = configPathWithoutRoot.split('/').slice(0, 4).join('/');
        title += ` ${shortPath}`;
    }
    document.title = title;
};

class ApplicationContainer extends React.Component {
    constructor(props) {
        super(props);
        this.appId = 1;
    }

    componentDidMount() {
        const { applicationName, version, onGetSystemSetting, path } = this.props;
        if (!version) {
            onGetSystemSetting();
        }
        setDocumentTitle(applicationName, path);
    }

    componentDidUpdate(prevProps) {
        const { applicationName, flags, path } = this.props;
        if (isDevEnv() && flags !== prevProps.flags) {
            this.appId += 1;
        }
        setDocumentTitle(applicationName, path);
    }

    render() {
        const { children } = this.props;
        return (
            <Application key={this.appId}>
                {children}
            </Application>
        );
    }
}

ApplicationContainer.propTypes = {
    applicationName: PropTypes.string,
    children: PropTypes.shape(Object),
    flags: PropTypes.shape(Object),
    onGetSystemSetting: PropTypes.func,
    path: PropTypes.string,
    version: PropTypes.string.isRequired,
};

ApplicationContainer.defaultProps = {
    version: '',
    path: '',
    onGetSystemSetting: () => (null),
};

const mapStateToProps = (state, ownProps) => ({
    applicationName: getApplicationName(state, ownProps),
    path: selectPathname(state),
    flags: selectFlags(state),
});

const mapDispatchToProps = dispatch => ({
    onGetSystemSetting() {
        dispatch(getSystemSetting());
    },
});

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(ApplicationContainer);

export {
    ApplicationContainer as _ApplicationContainer,
    mapDispatchToProps as _mapDispatchToProps,
};
