import * as actionTypes from './actionTypes';

const initialState = {
};

export default function confirm(state = initialState, action) {
    switch (action.type) {
    case actionTypes.SHOW_CONFIRM:
        return {
            callback: action.callback,
            message: action.message,
            title: action.title,
        };
    case actionTypes.HIDE_CONFIRM:
        return {};
    default:
        return state;
    }
}
