import React from 'react';
import '@webui-one/styles/styleSheetReset.css';
import PropTypes from 'prop-types';
import ShortcutComponent from 'Common/Components/ShortcutComponent/ShortcutComponent';
import ErrorHandlerContainer from 'Common/ErrorHandler/ErrorHandlerContainer';
import ConfirmContainer from 'Common/Confirm/ConfirmContainer';
import LoadingOverlayContainer from 'Common/LoadingOverlay/LoadingOverlayContainer';

const Application = props =>
    (<div data-handle="applicationRoot">
        <ShortcutComponent />
        { props.children }
        <ErrorHandlerContainer />
        <ConfirmContainer />
        <LoadingOverlayContainer />
    </div>);

Application.propTypes = {
    children: PropTypes.shape(Object),
};

export default Application;
