/* eslint-disable import/prefer-default-export */
import {
    getModelsThunk,
} from 'modules/models/actions';
import {
    getTransChangesAsyncAction,
} from 'modules/transactions/actions';
import {
    getPackagesAsyncAction,
} from 'modules/packages/actions';

export const getModelsAndPackages = () => (
    (dispatch) => {
        dispatch(getPackagesAsyncAction());
        return dispatch(getModelsThunk())
            .then(() => dispatch(getTransChangesAsyncAction()));
    }
);
