import { isDevEnv } from '@webui-one/core/utils/functional';
import { stringifyObject } from '@webui-one/core/utils/object';
import actionTypes from './actionTypes';

export const addErrorReportAction = (error, reason, date, id) => ({
    type: actionTypes.ERROR_REPORT_ADD,
    payload: {
        id,
        error,
        reason,
        date,
        isRead: false,
    },
});

export const addErrorReportFromErrorAction = (error, date = new Date()) => {
    if (isDevEnv()) {
        // eslint-disable-next-line no-console
        console.error(error);
    }

    return addErrorReportAction(
        error.message,
        error.data && stringifyObject(error.data),
        date,
        date.getTime(),
    );
};

export const resetErrorsAction = () => ({
    type: actionTypes.ERROR_REPORT_RESET,
    payload: null,
});

export const pruneErrorsAction = () => ({
    type: actionTypes.ERROR_REPORT_PRUNE,
    payload: null,
});

export const readErrorAction = id => ({
    type: actionTypes.ERROR_REPORT_READ,
    payload: { id },
});

export const setShouldIgnoreErrors = shouldIgnoreErrors => ({
    type: actionTypes.ERROR_REPORT_SET_IGNORE,
    payload: { shouldIgnoreErrors },
});
