import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { getSystemSetting } from 'modules/systemSetting/actions';

import { LOGOUT_REASON } from 'modules/logout/actions';
import {
    DEFAULT_START_WAIT_TIME,
    getCometChannel,
} from '@webui-one/core/comet';
import { setAuthenticated } from 'modules/application/actions';
import { UNSAFEWindowLocationHref } from '@webui-one/core/utils/unsafe';
import {
    upgradeCommitAction,
    upgradeCompleteAction,
    upgradeInitAction,
} from 'modules/transactions/actions';
import { isNonEmptyArray } from '@webui-one/core/utils/array';
import {
    isNetworkError as isNetworkErrorFunc,
    isUnloading,
} from '@webui-one/core/utils/browser';
import { isObject } from '@webui-one/core/utils/object';
import { isNonEmptyString } from '@webui-one/core/utils/string';
import {
    subscribeMessageHandlerAction,
    subscribeUpgradeHandlerAction,
    unsubscribeMessageHandlerAction,
    unsubscribeUpgradeHandlerAction,
} from 'modules/comet/actions';
import {
    selectCdbOperHandlers,
    selectChangesHandlers,
    selectMessageHandlers,
    selectUpgradeHandlers,
} from 'modules/comet/selectors';
import { getModelsAndPackages } from 'ConfigurationEditor/utils';

const TERMINATED_MSG = 'Your session has been terminated by';
const UPGRADE_COMPLETE_MSG = '>>> System upgrade has completed successfully.\n';
const CONNECTION_LOST = 'Connection lost';
const SESSION_INVALID = 'Session no longer valid';
const SESSION_TERMINATED = 'Session was terminated';
const INVALID_SESSION_ID = 'session.invalid_sessionid';

const handleInvalidSession = () => {
    window.sessionStorage.setItem(LOGOUT_REASON, SESSION_INVALID);
    UNSAFEWindowLocationHref('/login.html');
};

const handleConnectionLost = () => {
    window.sessionStorage.setItem(LOGOUT_REASON, CONNECTION_LOST);
    UNSAFEWindowLocationHref('/login.html');
};

const handleTerminatedSession = () => {
    window.sessionStorage.setItem(LOGOUT_REASON, SESSION_TERMINATED);
    UNSAFEWindowLocationHref('/login.html');
};

class CometContainer extends React.Component {
    constructor(props) {
        super(props);
        this.onError = this.onError.bind(this);
        this.onUpgrade = this.onUpgrade.bind(this);
        this.onMessage = this.onMessage.bind(this);
        this.state = {
            running: false,
        };
    }

    componentDidMount() {
        const {
            onSubscribeMessageHandler,
            onSubscribeUpgradeHandler,
            wait,
        } = this.props;
        this.props.onGetCometChannel().addErrorHandler(this.onError);
        this.props.onGetCometChannel().start(wait).then((running) => {
            this.setState({ running });
            onSubscribeMessageHandler(this.onMessage);
            onSubscribeUpgradeHandler(this.onUpgrade);
        });
    }

    componentDidUpdate(prevProps) {
        const {
            messageHandlers,
            changesHandlers,
            cdboperHandlers,
            upgradeHandlers,
        } = this.props;
        const {
            messageHandlers: prevMessageHandlers,
            changesHandlers: prevChangesHandlers,
            cdboperHandlers: prevCdbOperHandlers,
            upgradeHandlers: prevUpgradeHandlers,
        } = prevProps;

        if (prevCdbOperHandlers !== cdboperHandlers) {
            this.updateCdbOper(cdboperHandlers, prevCdbOperHandlers);
        }
        if (prevChangesHandlers !== changesHandlers) {
            this.updateChanges(changesHandlers, prevChangesHandlers);
        }
        if (messageHandlers !== prevMessageHandlers) {
            this.updateMessenger(messageHandlers, prevMessageHandlers);
        }
        if (upgradeHandlers !== prevUpgradeHandlers) {
            this.updateUpgrader(upgradeHandlers, prevUpgradeHandlers);
        }
    }

    componentWillUnmount() {
        const {
            onUnsubscribeMessageHandler,
            onUnsubscribeUpgradeHandler,
        } = this.props;
        onUnsubscribeMessageHandler(this.onMessage);
        onUnsubscribeUpgradeHandler(this.onUpgrade);
        this.props.onGetCometChannel().removeErrorHandler(this.onError);
    }

    onError(error, cometId, { funcs: { isNetworkError = isNetworkErrorFunc } = {} } = {}) {
        const {
            onConnectionLost,
            onInvalidSessionId,
        } = this.props;
        if (isNetworkError(error) && !isUnloading()) {
            onConnectionLost();
            return true;
        }
        if (error.type === INVALID_SESSION_ID) {
            onInvalidSessionId();
            return true;
        }
        return false;
    }

    onUpgrade(message) {
        const {
            onUpgradeInit,
            onUpgradeCommit,
        } = this.props;
        if (isObject(message) && message.upgrade_state === 'commit') {
            onUpgradeCommit();
        }
        if (isObject(message) && message.upgrade_state === 'init') {
            onUpgradeInit();
        }
    }

    onMessage(message) {
        const {
            onTerminated,
            onUpgradeComplete,
        } = this.props;
        if (isObject(message) && message.message === UPGRADE_COMPLETE_MSG) {
            onUpgradeComplete();
        }
        if (isObject(message)
            && isNonEmptyString(message.message)
            && message.message.startsWith(TERMINATED_MSG)
        ) {
            onTerminated();
            return true;
        }
        return false;
    }

    updateMessenger(current, prev) {
        const removeSubs = prev.filter(sub => !current.includes(sub));
        const newSubs = current.filter(sub => !prev.includes(sub));
        if (isNonEmptyArray(newSubs)) {
            newSubs.forEach(handler => (
                this.props.onGetCometChannel().subscribeMessages({}, handler)
            ));
        }
        if (isNonEmptyArray(removeSubs)) {
            removeSubs.forEach(handler => (
                this.props.onGetCometChannel().unsubscribeMessages({}, handler)
            ));
        }
    }

    updateUpgrader(current, prev) {
        const removeSubs = prev.filter(sub => !current.includes(sub));
        const newSubs = current.filter(sub => !prev.includes(sub));
        if (isNonEmptyArray(newSubs)) {
            newSubs.forEach(handler => (
                this.props.onGetCometChannel().subscribeUpgrade({}, handler)
            ));
        }
        if (isNonEmptyArray(removeSubs)) {
            removeSubs.forEach(handler => (
                this.props.onGetCometChannel().unsubscribeUpgrade({}, handler)
            ));
        }
    }

    updateChanges(current, prev) {
        const removeSubs = prev.filter(sub => !current.includes(sub));
        const newSubs = current.filter(sub => !prev.includes(sub));
        if (isNonEmptyArray(newSubs)) {
            newSubs.forEach(sub => (
                this.props.onGetCometChannel().subscribeChanges(
                    { path: sub.path },
                    sub.handler,
                )
            ));
        }
        if (isNonEmptyArray(removeSubs)) {
            removeSubs.forEach(sub => (
                this.props.onGetCometChannel().unsubscribeChanges(
                    { path: sub.path },
                    sub.handler,
                )
            ));
        }
    }

    updateCdbOper(current, prev) {
        const removeSubs = prev.filter(sub => !current.includes(sub));
        const newSubs = current.filter(sub => !prev.includes(sub));
        if (isNonEmptyArray(newSubs)) {
            newSubs.forEach(sub => (
                this.props.onGetCometChannel().subscribeCdbOper(
                    { path: sub.path },
                    sub.handler,
                )
            ));
        }
        if (isNonEmptyArray(removeSubs)) {
            removeSubs.forEach(sub => (
                this.props.onGetCometChannel().unsubscribeCdbOper(
                    { path: sub.path },
                    sub.handler,
                )
            ));
        }
    }

    render() {
        const { running } = this.state;
        return (<div data-comet-running={running} />);
    }
}

const SubscriptionType = PropTypes.shape({
    path: PropTypes.string,
    func: PropTypes.func,
});

CometContainer.propTypes = {
    cdboperHandlers: PropTypes.arrayOf(SubscriptionType),
    changesHandlers: PropTypes.arrayOf(SubscriptionType),
    messageHandlers: PropTypes.arrayOf(PropTypes.func),
    onConnectionLost: PropTypes.func.isRequired,
    onGetCometChannel: PropTypes.func.isRequired,
    onInvalidSessionId: PropTypes.func.isRequired,
    onSubscribeMessageHandler: PropTypes.func.isRequired,
    onSubscribeUpgradeHandler: PropTypes.func.isRequired,
    onTerminated: PropTypes.func.isRequired,
    onUnsubscribeMessageHandler: PropTypes.func.isRequired,
    onUnsubscribeUpgradeHandler: PropTypes.func.isRequired,
    onUpgradeInit: PropTypes.func,
    onUpgradeCommit: PropTypes.func,
    onUpgradeComplete: PropTypes.func,
    upgradeHandlers: PropTypes.arrayOf(PropTypes.func),
    wait: PropTypes.number,
};

CometContainer.defaultProps = {
    includeSubfolders: false,
    onGetCometChannel: getCometChannel,
    showActions: false,
    wait: DEFAULT_START_WAIT_TIME,
};

const emptyArray = Object.freeze([]);

const mapStateToProps = state => (
    {
        cdboperHandlers: selectCdbOperHandlers(state) || emptyArray,
        changesHandlers: selectChangesHandlers(state) || emptyArray,
        messageHandlers: selectMessageHandlers(state) || emptyArray,
        upgradeHandlers: selectUpgradeHandlers(state) || emptyArray,
    }
);

const mapDispatchToProps = dispatch => ({
    onConnectionLost() {
        dispatch(setAuthenticated(false));
        handleConnectionLost();
    },
    onInvalidSessionId() {
        dispatch(setAuthenticated(false));
        handleInvalidSession();
    },
    onSubscribeMessageHandler(handler) {
        dispatch(subscribeMessageHandlerAction(handler));
    },
    onUnsubscribeMessageHandler(handler) {
        dispatch(unsubscribeMessageHandlerAction(handler));
    },
    onSubscribeUpgradeHandler(handler) {
        dispatch(subscribeUpgradeHandlerAction(handler));
    },
    onTerminated() {
        dispatch(setAuthenticated(false));
        handleTerminatedSession();
    },
    onUnsubscribeUpgradeHandler(handler) {
        dispatch(unsubscribeUpgradeHandlerAction(handler));
    },
    onUpgradeCommit() {
        dispatch(upgradeCommitAction());
    },
    onUpgradeInit() {
        dispatch(upgradeInitAction());
    },
    onUpgradeComplete() {
        return dispatch(getSystemSetting('all', true)).then(() => {
            dispatch(upgradeCompleteAction());
            return dispatch(getModelsAndPackages());
        });
    },
});

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(CometContainer);

export {
    CometContainer as _CometContainer,
    handleConnectionLost,
    handleInvalidSession,
    handleTerminatedSession,
    mapDispatchToProps as _mapDispatchToProps,
    CONNECTION_LOST,
    INVALID_SESSION_ID,
    SESSION_INVALID,
    SESSION_TERMINATED,
    UPGRADE_COMPLETE_MSG,
};
