import * as actionTypes from 'modules/logout/actionTypes';

const initialState = {
    isPendingLogout: false,
};

export default function logout(state = initialState, action) {
    switch (action.type) {
    case actionTypes.LOGOUT:
        return {
            ...state,
            error: undefined,
            clearStorage: action.clearStorage,
            isPendingLogout: false,
        };
    case actionTypes.LOGOUT_SET_PENDING:
        return {
            ...state,
            isPendingLogout: action.isPendingLogout,
        };
    case actionTypes.LOGOUT_ERROR:
        return {
            ...state,
            error: action.error,
            isPendingLogout: false,
        };
    default:
        return state;
    }
}

export const selectLogout = state => (
    (state && state.logout) || {}
);

export const selectClearStorage = state => (
    selectLogout(state).clearStorage === true
);

export const selectIsPendingLogout = state => selectLogout(state).isPendingLogout;
