import * as actionTypes from './actionTypes';


export const subscribeChangesAction = (paths, handler) => ({
    type: actionTypes.SUBSCRIBE_CHANGES,
    paths,
    handler,
});

export const unsubscribeChangesAction = (paths, handler) => ({
    type: actionTypes.UNSUBSCRIBE_CHANGES,
    paths,
    handler,
});

export const subscribeCdbOperAction = (paths, handler) => ({
    type: actionTypes.SUBSCRIBE_CDB_OPER,
    paths,
    handler,
});

export const unsubscribeCdbOperAction = (paths, handler) => ({
    type: actionTypes.UNSUBSCRIBE_CDB_OPER,
    paths,
    handler,
});

export const subscribeMessageHandlerAction = handler => ({
    type: actionTypes.SUBSCRIBE_MESSAGE_HANDLER,
    handler,
});

export const unsubscribeMessageHandlerAction = handler => ({
    type: actionTypes.UNSUBSCRIBE_MESSAGE_HANDLER,
    handler,
});

export const subscribeUpgradeHandlerAction = handler => ({
    type: actionTypes.SUBSCRIBE_UPGRADE_HANDLER,
    handler,
});

export const unsubscribeUpgradeHandlerAction = handler => ({
    type: actionTypes.UNSUBSCRIBE_UPGRADE_HANDLER,
    handler,
});

