import * as actionTypes from './actionTypes';

export const initialState = {
    includeSubfolders: false,
    isEditMode: false,
    lastLocation: '',
    showActions: true,
    showEmpty: true,
    showOperdata: true,
};

export default function reducer(state = initialState, action = {}) {
    switch (action.type) {
    case actionTypes.INCLUDE_SUBFOLDERS:
        return {
            ...state,
            includeSubfolders: action.value,
        };
    case actionTypes.SHOW_ACTIONS:
        return {
            ...state,
            showActions: action.value,
        };
    case actionTypes.SHOW_EMPTY:
        return {
            ...state,
            showEmpty: action.value,
        };
    case actionTypes.SHOW_OPERDATA:
        return {
            ...state,
            showOperdata: action.value,
        };
    case actionTypes.SET_EDIT_MODE:
        return {
            ...state,
            isEditMode: action.value,
        };
    case actionTypes.SHOW_ALL:
        return {
            ...state,
            showOperdata: action.value,
            showEmpty: true,
            showActions: action.value,
        };
    case actionTypes.SET_LAST_LOCATION:
        return {
            ...state,
            lastLocation: action.lastLocation,
        };
    default:
        return state;
    }
}

export const selectApplicationConfiguration = state => state.applicationConfiguration;
export const selectIsEditMode = state => state.applicationConfiguration.isEditMode;
export const selectLastLocation = state => state.applicationConfiguration.lastLocation;
export const selectShowEmpty = state => state.applicationConfiguration.showEmpty;
export const selectShowOperdata = state =>
    state.applicationConfiguration.showOperdata && !!selectShowEmpty(state);
export const selectShowActions = state =>
    state.applicationConfiguration.showActions && !!selectShowEmpty(state);
export const selectIncludeSubfolders = state =>
    state.applicationConfiguration.includeSubfolders;
