import {
    selectApplicationName,
} from 'modules/application/selectors';

import { getRootPath } from '@webui-one/core/utils/browser';

export const selectRouting = state => state.routing;
export const selectLocationBeforeTransitions = state => (
    selectRouting(state).locationBeforeTransitions
);

export const selectPathname = state => (
    selectLocationBeforeTransitions(state).pathname
);

export const selectPathFromLocationPathname = (state) => {
    const prefix = `${getRootPath()}/${selectApplicationName(state)}`;
    const pathname = decodeURI(selectPathname(state));
    if (pathname.indexOf(prefix) === 0) {
        return pathname.slice(prefix.length);
    }
    return pathname;
};

export const selectPathFromApplicationName = (state, appName) => {
    const prefix = `${getRootPath()}/${appName}`;
    const pathname = decodeURIComponent(selectPathname(state));
    if (pathname.indexOf(prefix) === 0) {
        return pathname.slice(prefix.length);
    }
    return pathname;
};

export const selectLocationPathnameFromPath = (state, path) => (
    `${getRootPath()}/${selectApplicationName(state)}${path}`
);

export const selectLocationPathnameFromApplicationName = (appName, path) => (
    `${getRootPath()}/${appName}${path}`
);

export const selectIsRootPath = (state, path) => (
    (selectPathFromLocationPathname(state) === path)
);

