import {
    create,
    commit,
    deleteTrans,
    getValue,
    newTrans,
    setValue,
    validateCommit,
} from '@webui-one/core/jsonrpc';

import {
    DBS,
    MODES,
} from '@webui-one/core/jsonrpc/constants';

const directCommitOkAction = type => ({
    type: `${type}_OK`,
});
const directCommitStartAction = type => ({
    type: `${type}_START`,
});
const directCommitErrorAction = (type, error) => ({
    type: `${type}_ERROR`,
    error,
});

const directCommit = (type, tag, handler, {
    newTransFunc = newTrans,
    validateCommitFunc = validateCommit,
    commitFunc = commit,
    deleteTransFunc = deleteTrans,
} = {}) => (
    (dispatch) => {
        const mode = MODES.READ_WRITE;
        const db = DBS.RUNNING;
        const confMode = 'private';
        let handle;
        dispatch(directCommitStartAction(type));
        return newTransFunc({ db, mode, tag, confMode }).then((th) => {
            handle = th;
            return handler(th).then(() => (
                validateCommitFunc({ th })
                    .then(() => commitFunc({ th }))
                    .then(() => { handle = undefined; })
            )).then(() => (
                dispatch(directCommitOkAction(type))
            ));
        }).catch((error) => {
            if (handle) {
                deleteTransFunc({ th: handle });
            }
            dispatch(directCommitErrorAction(type, error));
        });
    }
);

export default directCommit;


export {
    commit,
    create,
    deleteTrans,
    directCommit,
    directCommitErrorAction,
    directCommitOkAction,
    directCommitStartAction,
    getValue,
    newTrans,
    setValue,
    validateCommit,
};
