import * as actionTypes from './actionTypes';

const initialState = {
    show: false,
};

const loadingOverlay = (state = initialState, action) => {
    switch (action.type) {
    case actionTypes.SHOW_LOADING_OVERLAY:
        return { show: true };
    case actionTypes.HIDE_LOADING_OVERLAY:
        return { show: false };
    default:
        return state;
    }
};

export const selectShow = state => state.loadingOverlay.show;

export default loadingOverlay;
