
import {
    read as readActionThunk,
    tryGetTransChanges,
} from 'modules/transactions/actions';
import * as applicationsActionTypes from 'modules/applications/actionTypes';
import * as nodesActionTypes from 'modules/nodes/actionTypes';
import * as dataActionTypes from 'modules/data/actionTypes';
import * as logoutActionTypes from 'modules/logout/actionTypes';
import * as packagesActionTypes from 'modules/packages/actionTypes';
import * as schemaActionTypes from 'modules/schemas/actionTypes';
import * as transactionActionTypes from 'modules/transactions/actionTypes';

import jsonrpcSimple from '@webui-one/core/jsonrpc';
import {
    debounceWithDispatch,
} from '@webui-one/core/utils/async';
import { isNonEmptyString } from '@webui-one/core/utils/string';
import {
    addErrorReportFromErrorAction as addErrorReportFromErrorActionCreator,
} from 'modules/error/actions';
import {
    getApplicationsAction,
    getApplicationsStartAction,
} from 'modules/applications/actions';
import {
    getPackages as getPackagesActionThunk,
} from 'modules/packages/actions';
import {
    queryAction,
} from 'modules/nodes/actions';

const errorActions = [
    dataActionTypes.GET_DATA_ERROR,
    logoutActionTypes.LOGOUT_ERROR,
    nodesActionTypes.CREATE_ERROR,
    nodesActionTypes.DELETE_ERROR,
    nodesActionTypes.EXISTS_ERROR,
    nodesActionTypes.GET_VALUE_ERROR,
    nodesActionTypes.QUERY_ERROR,
    schemaActionTypes.GET_SCHEMA_ERROR,
    transactionActionTypes.COMMIT_DRY_RUN_ERROR,
    transactionActionTypes.COMMIT_ERROR,
    transactionActionTypes.NATIVE_COMMIT_DRY_RUN_ERROR,
    transactionActionTypes.VALIDATE_COMMIT_ERROR,
];
const isErrorAction = action => (
    action &&
    isNonEmptyString(action.type) &&
    errorActions.includes(action.type)
);

const changeActions = [
    nodesActionTypes.CREATE,
    nodesActionTypes.SET_VALUE_COMPLETE,
    nodesActionTypes.DELETE,
    nodesActionTypes.DELETE_NODE_AND_CHILDREN,
    transactionActionTypes.GET_TRANS_CHANGES_ASYNC,
];

const isChangeAction = action => (
    action &&
    isNonEmptyString(action.type) &&
    changeActions.includes(action.type)
);

const isApplicationAction = action => (
    action &&
    isNonEmptyString(action.type) &&
    action.type === applicationsActionTypes.APPLICATIONS_GET_ASYNC
);

const isPackagesAction = action => (
    action &&
    isNonEmptyString(action.type) &&
    action.type === packagesActionTypes.PACKAGES_GET_ASYNC
);

export const query = (selection, path, {
    funcs: {
        readThunk = readActionThunk,
        jsonrpc = jsonrpcSimple,
    } = {},
} = {}) =>
    async (dispatch) => {
        const { th } = await dispatch(readThunk());
        const result = await jsonrpc('query', { th, path, selection });
        return dispatch(queryAction(result, selection, path));
    };

export const handleChanges = ({
    funcs: {
        tryGetTransChangesThunk = tryGetTransChanges,
        addErrorReportFromErrorAction = addErrorReportFromErrorActionCreator,
    } = {},
} = {}) =>
    async (dispatch) => {
        try {
            await dispatch(tryGetTransChangesThunk());
        } catch (err) {
            await dispatch(addErrorReportFromErrorAction(err));
        }
    };

export const handleJsonRpcError = (action, {
    funcs: {
        addErrorReportFromErrorAction = addErrorReportFromErrorActionCreator,
    } = {},
} = {}) =>
    dispatch =>
        dispatch(addErrorReportFromErrorAction(action.error));

export const getApplications = ({
    funcs: {
        addErrorReportFromErrorAction = addErrorReportFromErrorActionCreator,
        queryThunk = query,
    } = {},
} = {}) =>
    async (dispatch) => {
        const selection = [
            'id',
            'category',
            'href',
            'title',
            'description',
            'promo-label',
            'abbreviation',
            'shortcut',
            'hidden',
        ];
        const path = '/webui:webui/webui-one:applications/application';
        try {
            await dispatch(getApplicationsStartAction());
            const action = await dispatch(queryThunk(selection, path));
            await dispatch(
                getApplicationsAction(selection, action.result.results),
            );
        } catch (err) {
            await dispatch(addErrorReportFromErrorAction(err));
        }
    };

export const getPackages = ({
    funcs: {
        addErrorReportFromErrorAction = addErrorReportFromErrorActionCreator,
        getPackagesThunk = getPackagesActionThunk,
    } = {},
} = {}) =>
    async (dispatch) => {
        try {
            await dispatch(getPackagesThunk());
        } catch (err) {
            await dispatch(addErrorReportFromErrorAction(err));
        }
    };

const createSideEffectMiddleware = ({
    funcs: {
        getApplicationsThunk = getApplications,
        getPackagesThunk = getPackages,
        handleChangesThunk = handleChanges,
        handleJsonRpcErrorThunk = handleJsonRpcError,
    } = {},
} = {}) => {
    let changes;
    let handlers = [];
    const middleware = ({ dispatch }) => next => (action) => {
        handlers.forEach(([validator, handler]) => {
            if (validator(action)) {
                dispatch(handler(action));
            }
        });
        return next(action);
    };

    middleware.initialize = (store, handleChangesWaitTime = 300) => {
        changes = debounceWithDispatch(
            handleChangesThunk,
            {
                milliseconds: handleChangesWaitTime,
                waitWhileSpammed: true,
            },
        );

        store.dispatch(changes());

        handlers.push([isChangeAction, changes]);
        handlers.push([isPackagesAction, getPackagesThunk]);
        handlers.push([isApplicationAction, getApplicationsThunk]);
        handlers.push([isErrorAction, handleJsonRpcErrorThunk]);
    };

    middleware.cleanup = () => {
        if (changes) {
            changes.cancel();
            changes = undefined;
        }
        handlers = [];
    };

    return middleware;
};

export default createSideEffectMiddleware;
