/* eslint-disable import/prefer-default-export */
import jsonrpcSimple from '@webui-one/core/jsonrpc';

import { read } from 'modules/transactions/actions';
import {
    runCustomActionThunk as _runCustomActionThunk,
} from 'modules/yangActions/actions';

import { packageStatusUrl } from 'modules/packages/utils';
import { selectWebuiPackages } from 'modules/systemSetting/reducer';

import { query } from 'modules/nodes/actions';
import { getParentPath } from '@webui-one/core/utils/path';
import { zipWithProp } from '@webui-one/core/utils/array';
import * as actionTypes from './actionTypes';
import {
    RELOAD_PATH,
} from './paths';

export const packageStatusAction = (packageName, packageStatus) => ({
    type: actionTypes.PACKAGE_STATUS,
    package: packageName,
    status: packageStatus,
});

export const packageWebuiAction = packageName => ({
    type: actionTypes.PACKAGE_WEBUI,
    package: packageName,
    data: {},
});

export const packageGetCompleteAction = packages => ({
    type: actionTypes.PACKAGES_GET,
    packages,
});

export const reloadPackages = ({
    funcs: {
        runCustomActionThunk = _runCustomActionThunk,
    } = {},
} = {}) => dispatch => dispatch(runCustomActionThunk({ path: RELOAD_PATH }));


export const getPackagesAsyncAction = () => ({
    type: actionTypes.PACKAGES_GET_ASYNC,
});

const findWebuiPackages = packages => (
    dispatch => (
        Promise.all(packages.map(packageName => (
            dispatch(packageWebuiAction(packageName))
        )))
    )
);

const getPackageStatus = (
    packages,
    {
        funcs: {
            jsonrpc = jsonrpcSimple,
            readThunk = read,
        } = {},
    } = {},
) => (
    dispatch => (
        Promise.all(packages.map((packageName) => {
            const url = packageStatusUrl(packageName);
            return dispatch(readThunk()).then(({ th }) => (
                jsonrpc('get_case', {
                    th,
                    path: getParentPath(url),
                    choice: 'status',
                })
            )).then(result => (
                dispatch(packageStatusAction(packageName, result.case))
            ));
        }))
    )
);

function getPackages({
    funcs: {
        queryFunc = query,
        getPackageStatusFunc = getPackageStatus,
        findWebuiPackagesFunc = findWebuiPackages,
    } = {},
} = {}) {
    const selection = ['name', 'description', 'package-version'];
    const path = '/ncs:packages/package';

    return (dispatch, getState) => (
        dispatch(queryFunc({ selection, path })).then((action) => {
            const packages = zipWithProp(action.result.results, 'name');
            dispatch(packageGetCompleteAction(packages));
            const packageNames = Object.keys(packages);
            return dispatch(getPackageStatusFunc(packageNames)).then(() => (
                dispatch(findWebuiPackagesFunc(selectWebuiPackages(getState())))
            ));
        })
    );
}

export {
    findWebuiPackages,
    getPackages,
    getPackageStatus,
};
