import * as actionTypes from 'modules/shortcuts/actionTypes';
import { isNonEmptyString } from '@webui-one/core/utils/string';
import { isFunction } from '@webui-one/core/utils/functional';

const initialState = {};

const registerShortcut = (state, action) => {
    const shortcuts = Array.isArray(action.shortcut)
        ? action.shortcut
        : [action.shortcut];

    if (!isFunction(action.handler)) {
        return state;
    }

    const newState = shortcuts.filter(s => isNonEmptyString(s)).reduce((acc, s) => {
        if (!Array.isArray(acc[s])) {
            acc[s] = [];
        }
        if (acc[s].indexOf(action.handler) === -1) {
            acc[s].push(action.handler);
        }
        return acc;
    }, { ...state });
    return newState;
};

const unregisterShortcut = (state, action) => {
    const shortcuts = Array.isArray(action.shortcut)
        ? action.shortcut
        : [action.shortcut];
    const { handler } = action;

    if (Object.keys(state).filter(s => shortcuts.includes(s)).length === 0) {
        return state;
    }
    const newState = shortcuts.filter(s => isNonEmptyString(s)).reduce((acc, s) => {
        if (Array.isArray(acc[s])) {
            if (acc[s].indexOf(handler) !== -1) {
                acc[s] = acc[s].filter(h => h !== handler);
            }
            if (acc[s].length === 0) {
                delete acc[s];
            }
        }
        return acc;
    }, { ...state });

    return newState;
};

export default function reducer(state = initialState, action) {
    switch (action.type) {
    case actionTypes.SHORTCUT_REGISTER:
        return registerShortcut(state, action);
    case actionTypes.SHORTCUT_UNREGISTER:
        return unregisterShortcut(state, action);
    default:
        return state;
    }
}

