import { isObject } from '@webui-one/core/utils/object';

class MessageFormatter {
    constructor(message, locale) {
        this.message = message;
        this.locale = locale || 'en';
    }

    format(values) {
        if (!isObject(values)) {
            return this.message;
        }
        const entries = Object.entries(values).reduce((acc, [key, value]) => {
            const searchText = `{${key}}`;
            acc[searchText] = {
                key, value, searchText,
            };
            return acc;
        }, {});
        const reg = new RegExp(Object.keys(entries).join('|'), 'gi');
        const formattedText = this.message.replace(reg, match => (
            entries[match].value
        ));
        return formattedText;
    }
}

export default MessageFormatter;
