import * as actionTypes from 'modules/services/actionTypes';

export const reduceSearch = (state = {}, { value }) => ({
    ...state,
    value,
});

const reduceIsSelectedMeta = (meta = {}, name, isSelected) => ({
    [name]: {
        ...meta[name],
        isSelected,
    },
});

export const reduceExpanded = (state = {}, action) => {
    switch (action.type) {
    case actionTypes.SET_EXPANDED_ROW:
        return {
            ...state,
            [action.path]: {
                isExpanded: action.isExpanded,
                column: action.column,
            },
        };
    default:
        return state;
    }
};

export const initialState = {
    deployments: {
        ontime: 0,
        jeopardized: 0,
        violated: 0,
    },
    lastService: '',
};

export default function services(state = initialState, action) {
    switch (action.type) {
    case actionTypes.SET_SEARCH:
        return {
            ...state,
            search: reduceSearch(state.search, action),
        };
    case actionTypes.SERVICES_SELECT:
        return {
            ...state,
            services: action.names.reduce((acc, name) => ({
                ...acc,
                ...reduceIsSelectedMeta(acc, name, action.isSelected),
            }), state.services),
        };
    case actionTypes.SERVICE_POINTS_GET:
        return { ...state, servicePoints: action.service_points };
    case actionTypes.SERVICE_SET_LAST_SERVICE:
        return {
            ...state,
            search: '',
            lastService: action.lastService,
        };
    case actionTypes.SERVICE_DEPLOYMENTS_GET:
        return {
            ...state,
            deployments: action.deployments,
            deploymentsIsLoading: false,
        };
    case actionTypes.SERVICE_DEPLOYMENTS_GET_START: {
        const { deployments, ...rest } = state;
        return {
            ...rest,
            deploymentsIsLoading: true,
        };
    }
    case actionTypes.SERVICE_DEPLOYMENTS_GET_ERROR: {
        return {
            ...state,
            deploymentsIsLoading: false,
        };
    }
    case actionTypes.SERVICE_INSTANCE_COUNT_GET:
        return {
            ...state,
            instanceCountIsLoading: false,
            instanceCount: action.count,
        };
    case actionTypes.SERVICE_INSTANCE_COUNT_GET_START: {
        const { instanceCount, ...rest } = state;
        return {
            ...rest,
            instanceCountIsLoading: true,
        };
    }
    case actionTypes.SERVICE_INSTANCE_COUNT_GET_ERROR:
        return {
            ...state,
            instanceCountIsLoading: false,
        };
    case actionTypes.SET_EXPANDED_ROW:
        return {
            ...state,
            expandedRows: reduceExpanded(state.expandedRows, action),
        };
    case actionTypes.SERVICES_GET_START:
        return {
            ...state,
            isLoading: true,
            error: undefined,
        };
    case actionTypes.SERVICES_GET_COMPLETE:
    case actionTypes.SERVICES_GET_ERROR:
        return {
            ...state,
            isLoading: false,
            error: action.error,
        };
    default:
        return state;
    }
}
