import jsonrpcSimple from '@webui-one/core/jsonrpc';
import * as actionTypes from './actionTypes';
import { selectPending } from './reducer';

export const getSystemSettingAction = (systemSetting, operation, fromCache) => ({
    type: actionTypes.GET_SYSTEM_SETTING,
    systemSetting,
    operation,
    fromCache,
});

export const getSystemSettingStartAction = () => ({
    type: actionTypes.GET_SYSTEM_SETTING_START,
});

export const getSystemSetting = (
    operation = 'all',
    force = false,
    {
        funcs: {
            jsonrpc = jsonrpcSimple,
        } = {},
    } = {},
) => (
    (dispatch, getState) => {
        if (selectPending(getState())) {
            return Promise.resolve();
        }
        const systemSetting = getState().systemSetting;
        if (force !== true && Object.keys(systemSetting).length > 0) {
            return Promise.resolve(
                dispatch(getSystemSettingAction(systemSetting, operation, true)),
            );
        }

        dispatch(getSystemSettingStartAction());
        return jsonrpc('get_system_setting', { operation }).then(res => (
            dispatch(getSystemSettingAction(res, operation, false))
        ));
    }
);
