import * as actionTypes from 'modules/sessions/actionTypes';

const initialState = {
    sortOrder: {
        columnKey: 'name',
        isDesc: false,
    },
    kicked: {},
};

export default function sessions(state = initialState, action) {
    switch (action.type) {
    case actionTypes.SESSION_SET_SORT_ORDER:
        return {
            ...state,
            sortOrder: action.sortOrder,
        };
    case actionTypes.KICK_USER_COMPLETE:
        return {
            ...state,
            kicked: {
                ...state.kicked,
                [action.sessionId]: {
                    isKicked: true,
                },
            },
        };
    default:
        return state;
    }
}
