import * as actionTypes from './actionTypes';

export const ConfirmStatus = {
    CANCEL: 0,
    CONFIRM: 1,
};

export const showConfirm = (callback, message, title) => ({
    type: actionTypes.SHOW_CONFIRM,
    callback,
    message,
    title,
});

export const hideConfirm = () => ({
    type: actionTypes.HIDE_CONFIRM,
});

export const doConfirm = (message, title) => (
    dispatch => (
        new Promise((resolve) => {
            dispatch(
                showConfirm(
                    (status) => { resolve(status); },
                    message,
                    title,
                ),
            );
        })
    )
);
