import { formatPath } from '@webui-one/core/utils/path';

export const CONFIGURATION_EDITOR_NAME = 'ConfigurationEditor';

export default {
    path: formatPath(`${CONFIGURATION_EDITOR_NAME}*`),
    getComponent(nextState, cb) {
        import(/* webpackChunkName: "ConfigurationEditorContainer" */'./ConfigurationEditorContainer').then((ConfigurationEditorContainer) => {
            cb(null, ConfigurationEditorContainer.default);
        });
    },
};

export const getLinkWithPath = path => formatPath(CONFIGURATION_EDITOR_NAME + path);
