/* eslint-disable import/prefer-default-export */

import {
    emptyFilter,
    identityFilter,
} from '@webui-one/storage/storage-filters';
import {
    nodesFilter,
} from 'modules/nodes/storage-filters';
import {
    yangActionsFilter,
} from 'modules/yangActions/storage-filters';
import {
    applicationConfigurationFilter,
} from 'ConfigurationEditor/storage-filters';
import {
    servicesFilter,
} from 'modules/services/storage-filters';
import { selectAuthenticated } from 'modules/application/selectors';
import { selectClearStorage } from 'modules/logout/reducer';

const generalUnloadFilters = {
    applicationConfiguration: applicationConfigurationFilter,
    comet: emptyFilter,
    loadingOverlay: emptyFilter,
    data: emptyFilter,
    userdata: emptyFilter,
    nodes: nodesFilter,
    schemas: emptyFilter,
    schemas2: emptyFilter,
    services: servicesFilter,
    shortcuts: emptyFilter,
    systemSetting: emptyFilter,
    yangActions: yangActionsFilter,
    transactions: emptyFilter,
    types: identityFilter,
};

const nonAuthenticatedFilters = {
    applicationConfiguration: applicationConfigurationFilter,
    services: servicesFilter,
};

export const getApplicationStorageFilters = (state) => {
    if (selectClearStorage(state)) {
        return {
            defaultFilter: emptyFilter,
        };
    } else if (!selectAuthenticated(state)) {
        return {
            filters: nonAuthenticatedFilters,
            defaultFilter: emptyFilter,
            options: {
                authenticated: false,
            },
        };
    }
    return {
        filters: generalUnloadFilters,
        defaultFilter: identityFilter,
        options: {
            authenticated: true,
        },
    };
};

