import * as nodesActionTypes from 'modules/nodes/actionTypes';
import * as transactionActionTypes from 'modules/transactions/actionTypes';

import { isFunction } from '@webui-one/core/utils/functional';
import { ensureArray } from '@webui-one/core/utils/array';

import {
    ipcCommitComplete,
    ipcCommitStart,
    ipcCommitError,
    ipcCommitDryRunStart,
    ipcCommitDryRunComplete,
    ipcCommitDryRunError,
    ipcDeleteTrans,
    ipcErrorAction,
    ipcFormattedCommitDryRunStart,
    ipcFormattedCommitDryRunComplete,
    ipcFormattedCommitDryRunError,
    ipcNativeCommitDryRunStart,
    ipcNativeCommitDryRunComplete,
    ipcNativeCommitDryRunError,
    ipcTransUpdate,
} from './actions';
import * as ipcActionTypes from './actionTypes';
import * as ipcTypes from './ipcTypes';

const reduxToIpcTypes = {
    [nodesActionTypes.CREATE]:
        ipcTypes.TRANS_CHANGED,
    [nodesActionTypes.DELETE]:
        ipcTypes.TRANS_CHANGED,
    [nodesActionTypes.DELETE_NODE_AND_CHILDREN]:
        ipcTypes.TRANS_CHANGED,
    [nodesActionTypes.SET_VALUE_COMPLETE]:
        ipcTypes.TRANS_CHANGED,
    [transactionActionTypes.COMMIT_COMPLETE]:
        ipcTypes.COMMIT_COMPLETE,
    [transactionActionTypes.COMMIT_ERROR]:
        ipcTypes.COMMIT_ERROR,
    [transactionActionTypes.COMMIT_START]:
        ipcTypes.COMMIT_START,
    [transactionActionTypes.COMMIT_DRY_RUN_COMPLETE]:
        ipcTypes.COMMIT_DRY_RUN_COMPLETE,
    [transactionActionTypes.COMMIT_DRY_RUN_ERROR]:
        ipcTypes.COMMIT_DRY_RUN_ERROR,
    [transactionActionTypes.COMMIT_DRY_RUN_START]:
        ipcTypes.COMMIT_DRY_RUN_START,
    [transactionActionTypes.DATA_LOADED]:
        ipcTypes.LOAD_DATA,
    [transactionActionTypes.DELETE_TRANS]:
        ipcTypes.DELETE_TRANS,
    [transactionActionTypes.NATIVE_COMMIT_DRY_RUN_COMPLETE]:
        ipcTypes.NATIVE_COMMIT_DRY_RUN_COMPLETE,
    [transactionActionTypes.NATIVE_COMMIT_DRY_RUN_ERROR]:
        ipcTypes.NATIVE_COMMIT_DRY_RUN_ERROR,
    [transactionActionTypes.NATIVE_COMMIT_DRY_RUN_START]:
        ipcTypes.NATIVE_COMMIT_DRY_RUN_START,
    [transactionActionTypes.FORMATTED_COMMIT_DRY_RUN_COMPLETE]:
        ipcTypes.FORMATTED_COMMIT_DRY_RUN_COMPLETE,
    [transactionActionTypes.FORMATTED_COMMIT_DRY_RUN_ERROR]:
        ipcTypes.FORMATTED_COMMIT_DRY_RUN_ERROR,
    [transactionActionTypes.FORMATTED_COMMIT_DRY_RUN_START]:
        ipcTypes.FORMATTED_COMMIT_DRY_RUN_START,
};

const ipcToReduxActions = {
    [ipcTypes.COMMIT_COMPLETE]:
        ipcCommitComplete,
    [ipcTypes.COMMIT_ERROR]:
        ipcCommitError,
    [ipcTypes.COMMIT_START]:
        ipcCommitStart,
    [ipcTypes.COMMIT_DRY_RUN_COMPLETE]:
        ipcCommitDryRunComplete,
    [ipcTypes.COMMIT_DRY_RUN_ERROR]:
        ipcCommitDryRunError,
    [ipcTypes.COMMIT_DRY_RUN_START]:
        ipcCommitDryRunStart,
    [ipcTypes.DELETE_TRANS]:
        ipcDeleteTrans,
    [ipcTypes.FORMATTED_COMMIT_DRY_RUN_COMPLETE]:
        ipcFormattedCommitDryRunComplete,
    [ipcTypes.FORMATTED_COMMIT_DRY_RUN_ERROR]:
        ipcFormattedCommitDryRunError,
    [ipcTypes.FORMATTED_COMMIT_DRY_RUN_START]:
        ipcFormattedCommitDryRunStart,
    [ipcTypes.NATIVE_COMMIT_DRY_RUN_COMPLETE]:
        ipcNativeCommitDryRunComplete,
    [ipcTypes.NATIVE_COMMIT_DRY_RUN_ERROR]:
        ipcNativeCommitDryRunError,
    [ipcTypes.NATIVE_COMMIT_DRY_RUN_START]:
        ipcNativeCommitDryRunStart,
    [ipcTypes.LOAD_DATA]:
        ipcTransUpdate,
    [ipcTypes.TRANS_CHANGED]:
        ipcTransUpdate,
};

const sendStorageMessage = (type, data) => {
    const payload = {
        data,
        windowId: window.name,
        timestamp: Date.now(),
    };
    window.localStorage.setItem(type, JSON.stringify(payload));
};

const removeStorageMessage = (message) => {
    window.localStorage.removeItem(message.type);
};

const deserializeMessage = (type) => {
    const message = JSON.parse(window.localStorage.getItem(type));
    return {
        type,
        ...message,
    };
};

const isValidMessage = evt => (
    evt.storageArea === window.localStorage &&
    Object.values(ipcTypes).includes(evt.key) &&
    evt.newValue !== null && evt.newValue !== undefined
);

const getIpcAction = (type) => {
    if (!(type in ipcToReduxActions)) {
        throw new Error(`Unknown message type ${type}`);
    }
    return ipcToReduxActions[type];
};

const callSubscribers = (subscriptions, data) => {
    subscriptions.forEach((sub) => {
        if (sub.actionTypes.length === 0 || sub.actionTypes.includes(data.type)) {
            sub.handler(data);
        }
    });
};

const createStorageHandler = (
    {
        middleware,
        store,
        funcs: {
            getAction = getIpcAction,
            getMessage = deserializeMessage,
            removeMessage = removeStorageMessage,
            shouldProcessMessage = isValidMessage,
            notifySubscribers = callSubscribers,
        } = {},
    } = {}) => (evt) => {
    if (!store) {
        return false;
    }
    try {
        if (!shouldProcessMessage(evt)) {
            return false;
        }
        const message = getMessage(evt.key);
        if (window.name === message.windowId) {
            return false;
        }

        removeMessage(message);

        const action = getAction(message.type);

        middleware.setProcessing(true);
        store.dispatch(action(message.data));
        notifySubscribers(middleware.getSubscriptions(), message.data);
        middleware.setProcessing(false);

        return true;
    } catch (error) {
        store.dispatch(ipcErrorAction(error));
        return false;
    }
};

const createIpcMiddleware = ({
    funcs: {
        createHandler = createStorageHandler,
        sendIpcMessage = sendStorageMessage,
    } = {},
} = {}) => {
    let processing = true;
    let storageHandler;
    let subscriptions = [];

    const unsubscribe = (handler) => {
        subscriptions = subscriptions.filter(sub => (
            sub.handler !== handler
        ));
    };

    const subscribe = ({ actionTypes, handler }) => {
        if (!isFunction(handler)) {
            return;
        }
        const subscription = subscriptions.find(sub => (
            sub.handler === handler
        ));
        if (subscription) {
            subscription.actionTypes = ensureArray(actionTypes).slice();
        } else {
            subscriptions.push({
                handler,
                actionTypes: ensureArray(actionTypes).slice(),
            });
        }
    };

    const middleware = () => next => (action) => {
        const type = reduxToIpcTypes[action.type];
        if (type && !middleware.isProcessing()) {
            sendIpcMessage(type, action);
        }
        if (action.type === ipcActionTypes.IPC_SUBSCRIBE) {
            subscribe(action);
        }
        if (action.type === ipcActionTypes.IPC_UNSUBSCRIBE) {
            unsubscribe(action.handler);
        }
        return next(action);
    };

    middleware.getSubscriptions = () => subscriptions;

    middleware.isProcessing = () => processing;
    middleware.setProcessing = (p) => { processing = p; };

    middleware.initialize = (store) => {
        storageHandler = createHandler({ middleware, store });
        window.addEventListener('storage', storageHandler);
        middleware.setProcessing(false);
    };

    middleware.cleanup = () => {
        window.removeEventListener('storage', storageHandler);
        subscriptions = [];
        middleware.setProcessing(true);
    };

    return middleware;
};


export default createIpcMiddleware;

export {
    createStorageHandler as _createStorageHandler,
    deserializeMessage as _deserializeMessage,
    isValidMessage as _isValidMessage,
    getIpcAction as _getIpcAction,
    ipcToReduxActions as _ipcToReduxActions,
    removeStorageMessage as _removeStorageMessage,
    sendStorageMessage as _sendStorageMessage,
    callSubscribers as _callSubscribers,
};
