

import * as actionTypes from './actionTypes';
import { labelTypes } from './selectors';

const getKeyFromLabel = (label) => {
    if (label.type === labelTypes.SEARCH) return labelTypes.SEARCH;
    return label.type + label.name;
};

export const labels = (state = {}, action) => {
    const { label } = action;
    const labelKey = getKeyFromLabel(label);

    switch (action.type) {
    case actionTypes.LABEL_ADD: {
        return {
            ...state,
            [labelKey]: label,
        };
    }
    case actionTypes.LABEL_REMOVE: {
        // eslint-disable-next-line
        const { [labelKey]: _, ...rest } = state;
        return rest;
    }
    default:
        return state;
    }
};

export const reduceExpanded = (state = {}, action) => {
    switch (action.type) {
    case actionTypes.SET_EXPANDED_ROW:
        return {
            ...state,
            [action.path]: {
                isExpanded: action.isExpanded,
                column: action.column,
            },
        };
    default:
        return state;
    }
};

const initialState = {
    sortOrder: {
        columnKey: 'name',
        isDesc: false,
    },
    labels: {},
    devices: {},
    modules: {},
};

export default function devices(state = initialState, action) {
    switch (action.type) {
    case actionTypes.DEVICES_SELECT:
        return {
            ...state,
            devices: action.deviceNames.reduce((acc, deviceName) => ({
                ...acc,
                [deviceName]: {
                    ...state[deviceName],
                    isSelected: action.isSelected,
                },
            }), state.devices),
        };
    case actionTypes.LABEL_ADD:
    case actionTypes.LABEL_REMOVE: {
        return {
            ...state,
            labels: labels(state.labels, action),
        };
    }
    case actionTypes.LOAD_MODULES_START:
        return {
            ...state,
            modules: {
                ...state.modules,
                [action.path]: { isLoading: true },
            },
        };
    case actionTypes.LOAD_MODULES_COMPLETE:
        return {
            ...state,
            modules: {
                ...state.modules,
                [action.path]: {
                    isLoading: false,
                    modules: action.result,
                },
            },
        };
    case actionTypes.LOAD_MODULES_ERROR:
        return {
            ...state,
            modules: {
                ...state.modules,
                [action.path]: {
                    isLoading: false,
                    error: action.error,
                },
            },
        };
    case actionTypes.SHOW_ADD_DEVICE_MODAL:
        return {
            ...state,
            showAddDeviceModal: true,
        };
    case actionTypes.HIDE_ADD_DEVICE_MODAL:
        return {
            ...state,
            showAddDeviceModal: false,
        };
    case actionTypes.REMOVE_DEVICES_FROM_GROUP_START:
    case actionTypes.ADD_DEVICES_TO_GROUP_START:
        return {
            ...state,
            deviceGroupChangeIsPending: true,
        };
    case actionTypes.REMOVE_DEVICES_FROM_GROUP_COMPLETE:
    case actionTypes.REMOVE_DEVICES_FROM_GROUP_ERROR:
    case actionTypes.ADD_DEVICES_TO_GROUP_COMPLETE:
    case actionTypes.ADD_DEVICES_TO_GROUP_ERROR:
        return {
            ...state,
            deviceGroupChangeIsPending: false,
        };
    case actionTypes.SET_EXPANDED_ROW:
        return {
            ...state,
            expandedRows: reduceExpanded(state.expandedRows, action),
        };
    default:
        return state;
    }
}
