
const EmptyObject = Object.freeze({});
const EmptyArray = Object.freeze([]);
export const selectComet = state => state.comet || EmptyObject;
export const selectCdbOperHandlers = (state) => {
    const comet = selectComet(state);
    return Array.isArray(comet.cdboperHandlers)
        ? comet.cdboperHandlers
        : EmptyArray;
};
export const selectChangesHandlers = (state) => {
    const comet = selectComet(state);
    return Array.isArray(comet.changesHandlers)
        ? comet.changesHandlers
        : EmptyArray;
};
export const selectMessageHandlers = (state) => {
    const comet = selectComet(state);
    return Array.isArray(comet.messageHandlers)
        ? comet.messageHandlers
        : EmptyArray;
};
export const selectUpgradeHandlers = (state) => {
    const comet = selectComet(state);
    return Array.isArray(comet.upgradeHandlers)
        ? comet.upgradeHandlers
        : EmptyArray;
};
